<template>
  <div class="books">

    <header>
      <div class="btn">
        <img @click='back()' src="static/img/header/i-back.png">
      </div>

      <div class="top-menu">
        <div class="in-class" @click='in_class()'>课内</div>
        <div class="out-of-school" @click='out_of_school(true)'>课外</div>
      </div>
    </header>

    <div class="container">
		  <div class="search">
        <div class="search-">
          <div v-if="search_key == ''" class="propmt">   
            <img src="static/img/index/i-search.png">
            <span>搜索</span>
          </div>
          <input class="input" v-model="search_key" v-on:input="listenKeyword">
        </div>
      </div>
      <div class="left">
        <div class="v-for" v-for='(item, index) in class_' :id="index" :key='index' @click='class_click(index)'>
          {{ item }}
        </div>
      </div>
      <div class="right">
        <div class="b-v-for" v-for='(item, index) in books' :id="index" :key='index'>
          <div class="b-left" @click='book_click(item.left)'>
            <div class="img"><img src="static/img/read/book-thum.png"></div>
            <div class="title">{{ item.left.title }}</div>
          </div>
          <div v-if="item.right.title != undefined" class="b-right" @click='book_click(item.right)'>
            <div class="img"><img src="static/img/read/book-thum.png"></div>
            <div class="title">{{ item.right.title }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
import $ from "jquery"
  export default{
    name:"books",
    data(){
      return{
        search_key: "",
        class_: ["一年级","二年级","三年级","四年级","五年级","六年级","七年级","八年级","九年级"],
        books: [],
        class_index: 0,
        type: "课内"      // 课内/课外
      }
    },
    created(){

      var that = this;

      this.class_index = this.$store.state.books.class_index
      this.type = this.$store.state.books.type

      this.$nextTick(function(){
        
        that.class_click(this.class_index)
        if(that.type == "课外"){
          that.out_of_school(false)
        }
      })
    },
    methods:{
      back(){

        this.$router.push("read_single")
      },
      in_class(){

        $(".in-class").css({"background-color":"white","color":"#157EFB"})
        $(".out-of-school").css({"background-color":"transparent","color":"white"})
        this.$store.state.books.type = "课内"
        this.type =  this.$store.state.books.type
        this.request()
      },
      out_of_school(isRequest){

        $(".out-of-school").css({"background-color":"white","color":"#157EFB"})
        $(".in-class").css({"background-color":"transparent","color":"white"})
        this.$store.state.books.type = "课外"
        this.type =  this.$store.state.books.type
        if(isRequest){
          this.request()
        }
      },
      class_click(index){

        this.class_index = index
        this.$store.state.books.class_index = index

        $('.v-for').css("background-color","#F5F6F8")
        var f = $('.v-for')[index]
        $(f).css("background-color","white")
        this.request(index)
      },
      request(index){

        var that = this

        var postData = {
          "currentPage": 0,
          "pageSize": 20,
          "type": this.type,
          "grade": this.class_[index],
          "title": this.search_key
        }
        this.httpRequest_token_no_loading("read/api/v1/appRead/getArticleSingleList", postData, function(res){

          var arr = res.data

          that.books = []
          for(var i = 0; i < arr.length; i+=2){
            var item = {"left":"","right":""}
            item.left = arr[i]
            if(arr[i + 1] != undefined){
              item.right = arr[i + 1]
            }
            that.books.push(item)
          }
          console.log(that.books)
        })
      },
      listenKeyword(){

        this.request(this.class_index)
      },
      book_click(book){

        if(this.$store.state.record_read.type == "单篇阅读"){
          this.$router.push({
            name:"read_speed",
            query:{
              books_selected_class: this.$route.query.books_selected_class,
              title: book.title,
              thum: "thum-book3.png",
              articleId: book.id,
              wordCount: book.wordCount
            }
          })
        }else if(this.$store.state.record_read.type == "单篇检测"){
          this.$router.push({
            name:"read_content_qa",
            query:{
              books_selected_class: this.$route.query.books_selected_class,
              title: book.title,
              thum: "thum-book3.png",
              articleId: book.id,
              wordCount: book.wordCount
            }
          })
        }
      }
    }
  }
</script>
<style lang="less" scoped>
  .books{
    overflow: hidden;
    height: 100%;
    header{
      height: 90/50rem;
      border-bottom: 1/50rem solid  #D9D9D9;
      background-color: #1B9BFE;
      position: relative;
      .btn{
        position: absolute;
        img{
          width: 80/50rem;
        }
      }
      .top-menu{
        width: 400/50rem;
        height: 58/50rem;
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
        border: 2/50rem solid #D9D9D9;
        border-radius: 8/50rem;
        &>div{
          width: 50%;
          height: 100%;
          color: #157EFB;
          text-align: center;
          line-height: 58/50rem;
          font-size: 29/50rem;
        }
        .in-class{
          background-color: white;
          float: left;
        }
        .out-of-school{
          float: right;
          color: white;
        }
      }
    }
    .container{
      height: 100%;
      overflow: hidden;
      background-color: #F5F6F8;
      .search{
        width: 100%;
        height: 80/50rem;
        overflow: hidden;
        .search-{
          width: 670/50rem;
          height: 58/50rem;
          margin: 0 auto;
          margin-top: 11/50rem;
          border-radius: 8/50rem;
          text-align: left;
          line-height: 60/50rem;
          color: #B3B3B3;
          background-color: white;
          border: 1/50rem solid  #DDDDDD;
          .propmt{
            position: absolute;
            font-size: 27/50rem;
            img{
              width: 25/50rem;
              height: 25/50rem;
              margin-left: 16/50rem;
              vertical-align: middle;
              margin-top: -5/50rem;
            }
          }
          .input{
            width: 100%;
            height: 100%;
            padding-left: 16/50rem;
          }
          input::-ms-input-placeholder {
            font-size: 32/50rem;
            color: #999999;
          }
          input{
            outline: none;
            background: transparent;
            border: none;
            height: 100%;
            width: 100%;
          }
          input::-webkit-input-placeholder {
            font-size: 32/50rem;
            color: #999999;
          }
          input::-moz-input-placeholder {
            font-size: 32/50rem;
            color: #999999;
          }
        }
      }
      .left{
        width: 160/50rem;
        height: 100%;
        float: left;
        .v-for{
          height: 100/50rem;
          text-align: center;
          line-height: 100/50rem;
        }
      }
      .right{
        width: 590/50rem;
        height: calc(100% - 3.4rem);
        float: right;
        overflow: scroll;
        .b-v-for{
          &>div{
            width: 270/50rem;
            height: 250/50rem;
            margin-top: 15/50rem;
            .img{
              width: 100%;
              height: 180/50rem;
              img{
                width: 100%;
                height: 100%;
              }
            }
            .title{
              text-align: center;
              line-height: 70/50rem;
            }
          }
          .b-left{
            margin-left: 16/50rem;
            float: left;
          }
          .b-right{
            margin-right: 16/50rem;
            float: right;
          }
        }
      }
    }
  }
</style>


