<template>
  <div class="read">

    <header_index title="阅读"></header_index>
    
    <div class="container">
      <div class="btns">
        <div @click='toBooks()'><img src="static/img/read/i-btn-single-read.png"></div>
        <div @click='toIntrodu("少年闰土", 0, 3000)'><img src="static/img/read/i-btn-whole-read.png"></div>
      </div>
      <div class="nav">
        <div>文章列表<img src="static/img/read/i-arrow-read.png"></div>
      </div>
      <div v-if="read_list.length > 0" class="q-a">
        <div v-for='(item, index) in read_list' :id="index" :key='index' class="v-f-read">
          <div @click='toIntrodu(item.title, item.id, item.wordCount)'><p>{{ item.title }}</p><p>{{ item.updateTime }}</p></div>
        </div>
      </div>
      <div v-if="read_list.length == 0" class="no-data" @click='request_again'>
        <img :src="'static/img/read/i-no-data' + no_data_image_name + '.png'">
        <span>{{ no_data_prompt }}</span>
      </div>
    </div>
    <footer_index/>
  </div>
</template>
<script type="text/javascript">
import header_index from '../../components/header.vue'
import footer_index from '../../components/footer.vue'
import $ from "jquery"
  export default{
    name:"read",
    data(){
      return{
        read_list: [],
        no_data_prompt: "点击后刷新",
        no_data_image_name: "-refresh"
      }
    },
    components:{
      header_index,
      footer_index
    },
    created(){

      if(this.$store.state.read_list.length == 0){

        this.request()
      }else{

        this.read_list = this.$store.state.read_list
      }
    },
    methods:{
      toBooks(){

        this.$router.push({
          name:"books"
        })
      },
      request(){

        var that = this

        var postData = {
          "currentPage": 0,
          "pageSize": 20
        }
        // this.httpRequest_token("read/api/v1/appRead/getArticleSingleList", postData, function(res){

        //   that.read_list = res.data
        //   that.$store.state.read_list = that.read_list
          
        //   if(that.read_list.length == 0){
        //     that.no_data_prompt = "无数据"
        //   }

        //   // 删除重新请求队列
        //   let index = that.$store.state.request_timed_out_list.indexOf('read/api/v1/appRead/getArticleSingleList')
        //   if(index != -1){
        //     that.$store.state.request_timed_out_list.splice(index, 1)
        //   }
        // })
      },
      request_again(){

        if(this.$store.state.request_timed_out_list.indexOf("read/api/v1/appRead/getArticleSingleList") != -1){
          console.log("重新请求")
          this.request()
        }else{
          console.log("无操作")
        }
      },
      toIntrodu(title, articleId, wordCount){
        this.$router.push({
          name:"book_introdu",
          query:{
            title: title,
            thum: "thum-book3.png",
            articleId: articleId,
            wordCount: wordCount
          }
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  .read{
    height: 100%;
    overflow: hidden;
    .container{
      height: 100%;
      overflow: hidden;
      margin-left: 32/50rem;
      margin-right: 32/50rem;
      .btns{
        height: 218/50rem;
        &>div{
          height: 100%;
          img{
            width: 100%;
            height: 100%;
            display: block;
          }
          &:nth-child(1){
            float: left;
            width: 49%;
          }
          &:nth-child(2){
            float: right;
            width: 49%;
          }
        }
      }
      .nav{
        height: (47 + 60 + 44)/50rem;
        overflow:hidden;
        &>div{
          margin-top: 60/50rem;
          font-size: 36/50rem;
          margin-left: 28/50rem;
          img{
            height: 36/50rem;
            vertical-align: middle;
            margin-left: 20/50rem;
          }
        }
      }
      .q-a{
        margin: 0/50rem 5/50rem 5/50rem 5/50rem;
        box-shadow: 0 0 3px rgba(153, 153, 153, 0.5);
        height: calc(100% - 11.18rem);
        overflow: scroll;
        &>div{
          &>div{
            padding: 32/50rem;
            &:nth-child(1){
              border-bottom: 2/50rem solid  #D9D9D9;
            }
            p:nth-child(2){
              color: #999999;
              font-size: 26/50rem;
              margin-top: 10/50rem;
            }
          }
        }
      }
      .no-data{
        width: 100%;
        height: calc(100% - 11.18rem);
        img{
          width: 100/50rem;
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
          top: 60%;
        }
        span{
          width: 300/50rem;
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
          top: 66%;
          color: #aaa;
          text-align: center;
        }
      }
    }
  }
</style>


