<template>
  <div class="msg">
    
    <header_index title="高效速读"></header_index>
    
    <div class="container">

      <div class="noti-sear-his">

        <div class="i-noti">
          <div><img src="static/img/login/i-noti.png"></div>
          <div>通知</div>
        </div>

        <div class="i-sear">
          <div><img src="static/img/login/i-sear.png"></div>
          <div>搜索</div>
        </div>

        <div class="i-his">
          <div><img src="static/img/login/i-his.png"></div>
          <div>足迹</div>
        </div>

      </div>

      <div class="k-1">精彩推送</div>

      <div class="read-btn">
        <div class="bg"><img src="static/img/login/read-container.png"></div>
        <div class="read-btn-container">
          <div @click="single()">
            <img src="static/img/login/single.png">
            <div class="single-btn">单篇阅读</div>
          </div>
          <div>
            <img src="static/img/login/whole.png">
            <div class="whole-btn">整本阅读</div>
          </div>
        </div>
      </div>

    </div>
    <footer_index/>
  </div>
</template>
<script type="text/javascript">
import header_index from '../../components/header.vue'
import footer_index from '../../components/footer.vue'
  export default{
    name:"msg",
    data(){
      return{
        msg_list:[],
      }
    },
    components:{
      header_index,
      footer_index
    },
    created(){

      var that = this
    },
    methods:{
      single(){
        this.$router.push({
          name:"read_single"
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  .msg{
    height: 100%;
    overflow: hidden;
    .container{
      margin-left: 32/50rem;
      margin-right: 32/50rem;
      height: 100%;
      .noti-sear-his{
        margin-top: 20/50rem;
        height: 150/50rem;
        &>div{
          width: 33.3%;
          height: 100%;
          float: left;
          &>div{
            &:nth-child(1){
              width: 40%;
              img{
                width: 100%;
              }
            }
            &:nth-child(2){
              width: 40%;
              text-align: center;
            }
          }
        }
        .i-noti{
          &>div{
            margin-left: 30/50rem;
          }
        }
        .i-sear{
          &>div{
            margin: 0 auto;
          }
        }
        .i-his{
          &>div{
            margin-left: 110/50rem;
          }
        }
      }
      .k-1{
        margin-top: 30/50rem;
        height: 200/50rem;
        background-color: #8DB3FF;
        border-radius: 8/50rem;
      }
      .read-btn{
        margin-top: 30/50rem;
        height: 280/50rem;
        .bg{
          width: calc(100% - 1.28rem);
          position: absolute;
          z-index: -1;
          img{
            width: 100%;
            height: 100%;
          }
        }
        .read-btn-container{
          width: 100%;
          height: 100%;
          &>div{
            width: 50%;
            height: 100%;
            text-align: center;
            img{
              width: 30%;
            }
            &:nth-child(1){
              float: left;
              img{
                margin-top: 55/50rem;
                margin-left: 40/50rem;
              }
            }
            &:nth-child(2){
              float: right;
              img{
                margin-top: 45/50rem;
                margin-right: 40/50rem;
              }
            }
            .single-btn{
              padding-left: 40/50rem;
              margin-top: -30/50rem;
            }
            .whole-btn{
              padding-right: 40/50rem;
              margin-top: -40/50rem;
            }
          }
        }
      }
    }
  }
</style>


