<template>
  <div class="read_single">

    <header_index title='单篇阅读' :back_icon='true' @back='back'></header_index>

    <div class="container">

      <div class="img-top">
        <img src="static/img/read/i-read-single-top.png">
      </div>

      <div class="btn">
        <div @click='toTrain()'>
          <img src="static/img/read/i-btn-single-read-.png">
          <span>单篇训练</span>
        </div>
        <div @click='toExam()'>
          <img src="static/img/read/i-btn-whole-read-.png">
          <span>单篇检测</span>
        </div>
      </div>

    </div>

    <div class="img-bottom">
      <img src="static/img/read/i-read-single-bottom.png">
    </div>

  </div>
</template>
<script type="text/javascript">
import header_index from '../../components/header.vue'
import $ from "jquery"
  export default{
    name:"read_single",
    data(){
      return{
        title: "",
        books_selected_class: "",   // 几年级的书
        thum: "",                   // 略缩图
        read_single_title: "",      // 标题
        wordCount: "",              // 总字数
        articleId: ""               // 文章id
      }
    },
    components:{
      header_index,
    },
    created(){

      if(this.$route.query.title){
        this.title = this.$route.query.title
      }
      if(this.$route.query.books_selected_class){
        this.books_selected_class = this.$route.query.books_selected_class
      }
      if(this.$route.query.thum){
        this.thum = this.$route.query.thum
      }
      if(this.$route.query.read_single_title){
        this.read_single_title = this.$route.query.read_single_title
      }
      if(this.$route.query.wordCount){
        this.wordCount = this.$route.query.wordCount
      }
      if(this.$route.query.articleId){
        this.articleId = this.$route.query.articleId
      }

    },
    methods:{
      back(){

        this.$router.push({
          name:"index",
          query:{
            books_selected_class: this.$route.query.books_selected_class,
          }
        })
      },
      toTrain(){

        this.$store.state.record_read.type = "单篇阅读"
        
        this.$router.push("books")
      },
      toExam(){

        this.$store.state.record_read.type = "单篇检测"

        this.$router.push("books")
      }
    }
  }
</script>
<style lang="less" scoped>
  .read_single{
    height: 100%;
    overflow: hidden;
    .container{
      height: 100%;
      overflow: hidden;
      margin-left: 75/50rem;
      margin-right: 75/50rem;
      .img-top{
        width: 600/50rem;
        height: 361/50rem;
        margin-top: 94/50rem;
        img{
          width: 100%;
        }
      }
      .btn{
        width: 600/50rem;
        height: 110/50rem;
        margin-top: 67/50rem;
        &>div{
          width: 260/50rem;
          height: 100%;
          line-height: 90/50rem;
          border-radius: 8/50rem;
          color: white;
          position: relative;
          img{
            width: 100%;
            height: 100%;
            float: left;
            position: absolute;
            z-index: -1;
          }
          span{
            display: block;
            text-align: center;
          }
          &:nth-child(1){
            float: left;
          }
          &:nth-child(2){
            float: right;
          }
        }
      }
    }
    .img-bottom{
      height: 308/50rem;
      position: absolute;
      bottom: 0;
      img{
        width: 100%;
      }
    }
  }
</style>


