<template>
  <div class="read_speed">

    <header_index title='速度选择' :back_icon='true' @back='back'></header_index>

    <div class="container">

      <div class="range">

        <div class="prompt-1">速度范围（字/分钟）</div>

        <div v-for='(item, index) in range' :id="index" :key='index' class="v-f-range" @click='itemClick(index, true)'>
          <div class="line">
            <div class="point-start"></div>
            <div class="point-end"></div>
            <img class="point-start-confirm" src="static/img/read/i-speed.png">
            <img class="point-end-confirm" src="static/img/read/i-speed.png">
          </div>
          <div class="text">{{ item }}</div>
          <div v-if="index == range.length - 1" class="text-last">{{ range_last }}</div>
        </div>

      </div>

      <div class="speed">
        <div class="prompt-2">速度调节（字/分钟）</div>
        <div class="prompt-3">阅读速度{{ word_per_minute }} 字/分钟</div>
        <div class="add-cut">
          <img class="btn-bg" src="static/img/read/i-speed-btn-bg.png">
          <img class="cut" src="static/img/read/i-speed-btn-cut.png" @click='cut_click()'>
          <input class="i-input" v-model="word_per_minute" v-on:input="listenKeyword">
          <img class="add" src="static/img/read/i-speed-btn-add.png" @click='add_click()'>
        </div>
      </div>

      <div class="start" @click='next()'><img src="static/img/read/i-read-start.png"></div>
		
    </div>
  </div>
</template>
<script type="text/javascript">
import header_index from '../../components/header.vue'
import $ from "jquery"
  export default{
    name:"read_speed",
    data(){
      return{
        title: "",                  // 标题
        books_selected_class: "",   // 几年级的书
        thum: "",                   // 略缩图
        read_single_title: "",      // 标题
        wordCount: "",              // 总字数
        word_per_minute: 2400,      // 字/分钟
        word_per_line: 12,          // 字/行
        articleId: 0,               // 文章id
        selected_index: 0,          // 被选中的区间，从0开始
        range: [200,1000,3000,5000,25000],  // 阅读速度范围
        range_last: 0,
        min: 0,                     // 最小速度
        max: 0,                     // 最大速度
      }
    },
    components:{
      header_index,
    },
    created(){

      var that = this

      if(this.$route.query.title){
        this.title = this.$route.query.title
      }
      if(this.$route.query.books_selected_class){
        this.books_selected_class = this.$route.query.books_selected_class
      }
      if(this.$route.query.thum){
        this.thum = this.$route.query.thum
      }
      if(this.$route.query.read_single_title){
        this.read_single_title = this.$route.query.read_single_title
      }
      if(this.$route.query.wordCount){
        this.wordCount = this.$route.query.wordCount
      }
      if(this.$route.query.articleId){
        this.articleId = this.$route.query.articleId
      }

      // 处理数据，取出后最一个元素
      if(this.range.length > 0){
        this.range_last = this.range[this.range.length - 1]
        this.range.splice(this.range.length - 1, 1)
      }

      this.$nextTick(function(){

        var rem = 675 / that.range.length / 50 + "rem"
        $(".v-f-range").css("width",rem)

        $(".line").each(function(index,item){
          if(index == 0){
            $(this).css("borderRadius","1rem 0 0 1rem")
          }
          if(index == (that.range.length - 1)){
            $(this).css("borderRadius","0 1rem 1rem 0")
          }
        })

        $(".point-end").each(function(index,item){
          if(index == (that.range.length - 1)){
            $(this).css("display","block")
          }
        })
      })

      this.selected_index = this.$store.state.read_speed_and_range.range
      this.itemClick(this.selected_index, false)
      this.word_per_minute = this.$store.state.read_speed_and_range.speed
    },
    methods:{
      back(){

        this.$router.push("books")
      },
      itemClick(index, change_word_per_minute){

        var that = this

        this.selected_index = index

        this.min = this.range[index]
        if(index == this.range.length - 1){
          this.max = this.range_last
        }else{
          this.max = this.range[index + 1]
        }
        if(change_word_per_minute == true){
          this.word_per_minute = this.min
        }

        this.$nextTick(function(){

          $(".point-start-confirm").each(function(index,item){
            // 初始化清屏
            $(this).css("display","none")
            $(".point-end-confirm").css("display","none")
            // 赋值
            if(that.selected_index + 1 < that.range.length){
              if(index == that.selected_index || index == that.selected_index + 1){
                $(this).css("display","block")
              }
            }else{
              var end = $(".point-end-confirm")[that.range.length - 1]
              $(end).css("display","block")
              if(index == that.selected_index){
                $(this).css("display","block")
              }
            }
          })
        })
      },
      cut_click(){

        var r = parseInt(this.word_per_minute) - 200
        if(r >= this.min){
          this.word_per_minute = r
        }
      },
      add_click(){

        var r = parseInt(this.word_per_minute) + 200
        if(r <= this.max){
          this.word_per_minute = r
        }
      },
      next(){

        var that = this

        this.$store.state.read_speed_and_range.speed = this.word_per_minute
        this.$store.state.read_speed_and_range.range = this.selected_index

        var postData = {
          "speed": this.word_per_minute
        }

        this.httpRequest_token_no_loading("read/api/v1/appRead/getSpeedWordNumber", postData, function(res){

          that.$router.push({
            name:"read_ready",
            query:{
              title: that.title,
              books_selected_class: that.books_selected_class,
              thum: that.thum,
              read_single_title: that.$route.query.read_single_title,
              articleId: that.$route.query.articleId,
              wordCount: that.wordCount,
              word_per_minute: that.word_per_minute,
              word_per_line: res.data.wordNumber
            }
          })
        })
      },
      listenKeyword(){
        
        if(this.word_per_minute < 200){
          this.word_per_minute = 200
          this.selected_index = 0
        }else if(this.word_per_minute >= 200 && this.word_per_minute < 1000){
          this.selected_index = 0
        }else if(this.word_per_minute >= 1000 && this.word_per_minute < 3000){
          this.selected_index = 1
        }else if(this.word_per_minute >= 3000 && this.word_per_minute < 5000){
          this.selected_index = 2
        }else if(this.word_per_minute >= 5000 && this.word_per_minute <= 25000){
          this.selected_index = 3
        }else if(this.word_per_minute > 25000){
          this.word_per_minute = 25000
          this.selected_index = 3
        }
        this.itemClick(this.selected_index, false)
        this.$store.state.read_speed_and_range.range = this.selected_index
        this.$store.state.read_speed_and_range.speed = this.word_per_minute
      }
    }
  }
</script>
<style lang="less" scoped>
  .read_speed{
    width: 100%;
    height: 100%;
    overflow: hidden;
    .container{
      height: calc(100% - 1.85rem);
      padding-left: 37.5/50rem;
      padding-right: 37.5/50rem;
      background-color: #EBF9FD;
      position: absolute;
      z-index: 0;
      .range{
        height: 210/50rem;
        .prompt-1{
          color: #666666;
          font-size: 28/50rem;
          padding-top: 35/50rem;
        }
        .v-f-range{
          width: (675 / 4)/50rem;
          height: 110/50rem;
          float: left;
          margin-top: 30/50rem;
          position: relative;
          .line{
            width: 100%;
            height: 16/50rem;
            background-color: #74b7d7;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            // border-radius: 8/50rem 0 0 8/50rem;
            .point-start{
              width: 11/50rem;
              height: 11/50rem;
              background-color: #1b9bfe;
              border-radius: 8/50rem;
              border: 2.5/50rem solid white;
              float: left;
            }
            .point-end{
              width: 11/50rem;
              height: 11/50rem;
              background-color: #1b9bfe;
              border-radius: 8/50rem;
              border: 2.5/50rem solid white;
              float: right;
              display: none;
            }
            .point-start-confirm{
              height: 350%;
              float: left;
              margin-top: -30/50rem;
              margin-left: -30/50rem;
              display: none;
            }
            .point-end-confirm{
              height: 350%;
              float: right;
              margin-top: -30/50rem;
              margin-right: -31/50rem;
              display: none;
            }
          }
          .text{
            margin-top: 80/50rem;
            font-size: 26/50rem;
            color: #777777;
            margin-left: -18/50rem;
            float: left;
          }
          .text-last{
            margin-top: 80/50rem;
            font-size: 26/50rem;
            color: #777777;
            margin-right: -10/50rem;
            float: right;
          }
        }
      }
      .speed{
        height: 360/50rem;
        .prompt-2{
          padding-top: 60/50rem;
          color: #666666;
          font-size: 28/50rem;
        }
        .prompt-3{
          text-align: center;
          margin-top: 60/50rem;
          font-weight: bold;
          font-size: 36/50rem;
        }
        .add-cut{
          width: 450/50rem;
          height: 105/50rem;
          margin: 60/50rem auto;
          position: relative;
          .btn-bg{
            width: 100%;
            position: absolute;
            z-index: -1;
          }
          .cut{
            width: 60/50rem;
            padding: 23/50rem;
            float: left;
          }
          .i-input{
            width: (450 - 60 - 46 - 60 - 46)/50rem;
            line-height: 105/50rem;
            position: absolute;
            color: white;
            font-size: 39/50rem;
            height: 100%;
            text-align: center;
          }
          input{
            outline: none;
            background: transparent;
            border: none;
            height: 100%;
            width: 100%;
          }
          input::-webkit-input-placeholder {
            font-size: 32/50rem;
            color: #999999;
          }
          input::-moz-input-placeholder {
            font-size: 32/50rem;
            color: #999999;
          }
          input::-ms-input-placeholder {
            font-size: 32/50rem;
            color: #999999;
          }
          .add{
            width: 60/50rem;
            padding: 23/50rem;
            float: right;
          }
        }
      }
      .start{
        width: 294/50rem;
        height: 96/50rem;
        margin: 150/50rem auto;
        img{
          width: 100%;
        }
      }
    }
  }
</style>


