<template>
  <div class="read_qa">

    <header>
      <div class="title">
        {{ title }}<span>（{{ $route.query.wordCount }}字）</span>
      </div>
      <div class="btn">
        <img @click='back()' src="static/img/header/i-back.png">
      </div>
      <div class="timer">
        <span>{{ dealTime(accum_time) }}</span>
      </div>
    </header>

    <div class="container">

      <div class="bg-d">

        <img class="bg-img" src="static/img/read/i-qa-bg.png">

        <div class="qa-content">
          
          <div class="qa-title">{{ index }}、{{ topicList[index - 1].title }}</div>

          <div v-if="topicList[index - 1].questionOneKey != ''" class="one-d" @click="ok_click('a')">
            <div class="a-title">A. {{ topicList[index - 1].questionOneKey }}</div>
            <div class="check-d">
              <img class="a-img" v-if='allow_answer == true || (allow_answer == false && selected_tag !="a" && !correct_answer("a", index))' src="static/img/read/i-check-none.png">
              <img class="a-img" v-if='allow_answer == false && correct_answer("a", index)' src="static/img/read/i-check-true.png">
              <img class="a-img" v-if='allow_answer == false && !correct_answer("a", index) && selected_tag == "a"' src="static/img/read/i-check-false.png">
            </div>
          </div>

          <div v-if="topicList[index - 1].questionTwoKey != ''" class="two-d" @click="ok_click('b')">
            <div class="b-title">B. {{ topicList[index - 1].questionTwoKey }}</div>
            <div class="check-d">
              <img class="b-img" v-if='allow_answer == true || (allow_answer == false && selected_tag != "b" && !correct_answer("b", index))' src="static/img/read/i-check-none.png">
              <img class="b-img" v-if='allow_answer == false && correct_answer("b", index)' src="static/img/read/i-check-true.png">
              <img class="b-img" v-if='allow_answer == false && !correct_answer("b", index) && selected_tag == "b"' src="static/img/read/i-check-false.png">
            </div>
          </div>

          <div v-if="topicList[index - 1].questionThreeKey != ''" class="three-d" @click="ok_click('c')">
            <div class="c-title">C. {{ topicList[index - 1].questionThreeKey }}</div>
            <div class="check-d">
              <img class="c-img" v-if='allow_answer == true || (allow_answer == false && selected_tag !="c" && !correct_answer("c", index))' src="static/img/read/i-check-none.png">
              <img class="c-img" v-if='allow_answer == false && correct_answer("c", index)' src="static/img/read/i-check-true.png">
              <img class="c-img" v-if='allow_answer == false && !correct_answer("c", index) && selected_tag == "c"' src="static/img/read/i-check-false.png">
            </div>
          </div>

          <div v-if="topicList[index - 1].questionFourKey != ''" class="four-d" @click="ok_click('d')">
             <div class="d-title">D. {{ topicList[index - 1].questionFourKey }}</div>
            <div class="check-d">
              <img class="d-img" v-if='allow_answer == true || (allow_answer == false && selected_tag !="d" && !correct_answer("d", index))' src="static/img/read/i-check-none.png">
              <img class="d-img" v-if='allow_answer == false && correct_answer("d", index)' src="static/img/read/i-check-true.png">
              <img class="d-img" v-if='allow_answer == false && !correct_answer("d", index) && selected_tag == "d"' src="static/img/read/i-check-false.png">
            </div>
          </div>

        </div>

      </div>

    </div>
  </div>
</template>
<script type="text/javascript">
import header_index from '../../components/header.vue'
import $ from "jquery"
  export default{
    name:"read_qa",
    data(){
      return{
        topicList: [],      // 题目
        index: 1,           // 第几题
        allow_answer: true, // 是否允许答题
        correct_count: 0,   // 答对数量
        wordCount: 0,       // 文章字数
        read_time: 0,       // 阅读时间
        accum_time: 0,      // 累计时间
        title: "",          // 文章标题
        timer: "",          // 定时器
        selected_tag: ""    // 用户选择 a b c d
      }
    },
    components:{
      header_index
    },
    created(){

      var that = this

      if(this.$route.query.topicList){
        this.topicList = this.$route.query.topicList
      }
      if(this.$route.query.index){
        this.index = this.$route.query.index
      }
      if(this.$route.query.wordCount){
        this.wordCount = this.$route.query.wordCount
      }
      if(this.$route.query.accum_time){
        this.accum_time = this.$route.query.accum_time
        this.read_time = this.$route.query.accum_time
      }
      if(this.$route.query.title){
        this.title = this.$route.query.title
      }
      console.log(this.topicList)

      if(this.index == 1){
        
        // 记录阅读信息,存入存折
        // var postData = {

        // }
        // this.httpRequest_token("read/api/v1/appRead/getArticleById", postData, function(res){

        // })
      }

      this.start()

      this.cul_height()
    },
    beforeDestroy(){

      console.log("对象准备死掉")
      // 去除定时器
      window.clearInterval(this.timer)  
    },
    methods:{
      back(){

        this.$router.push("books")
      },
      correct_answer(t, index){
        if(t == "a"){
          if(this.topicList[index - 1].questionOneVal == this.topicList[index - 1].correctAnswer){
            return true
          }else{
            return false
          }
        }else if(t == "b"){
          if(this.topicList[index - 1].questionTwoVal == this.topicList[index - 1].correctAnswer){
            return true
          }else{
            return false
          }
        }else if(t == "c"){
          if(this.topicList[index - 1].questionThreeVal == this.topicList[index - 1].correctAnswer){
            return true
          }else{
            return false
          }
        }else if(t == "d"){
          if(this.topicList[index - 1].questionFourVal == this.topicList[index - 1].correctAnswer){
            return true
          }else{
            return false
          }
        }
      },
      ok_click(item){

        this.selected_tag = item

        if(this.allow_answer == false){
          console.log("答题准备中...")
          return
        }

        var that = this

        this.allow_answer = false

        this.$nextTick(function(){

          var src = $('.' + item + "-img")[0].src
          if(src.indexOf("true") != -1){
            that.correct_count ++
          }else{
            $('.' + item + '-title').css('color', 'red')
          }
          src = $('.a-img')[0].src
          if(src.indexOf("true") != -1){
            $('.a-title').css('color', 'blue')
          }
          src = $('.b-img')[0].src
          console.log(src)
          if(src.indexOf("true") != -1){
            $('.b-title').css('color', 'blue')
          }
          src = $('.c-img')[0].src
          if(src.indexOf("true") != -1){
            $('.c-title').css('color', 'blue')
          }
          src = $('.d-img')[0].src
          if(src.indexOf("true") != -1){
            $('.d-title').css('color', 'blue')
          }
        })
        setTimeout(function () {
          // 下一题
          that.next(item)  
          that.allow_answer = true
          $('.a-title').css('color', 'black')
          $('.b-title').css('color', 'black')
          $('.c-title').css('color', 'black')
          $('.d-title').css('color', 'black')
        }, 1000)
      },
      // 根据题目标题高度，动态更改背景图片高度
      cul_height(){

        this.$nextTick(function(){

          // 题目高度
          var title_add_h_rem = $('.qa-title').css('height')
          // 去掉px
          title_add_h_rem = title_add_h_rem.replace(/px/g, "")
          // 转换成rem
          title_add_h_rem = title_add_h_rem * 2 / 50
          // 计算增量，以42为标准
          title_add_h_rem = title_add_h_rem - 42 / 50
          console.log("题目高度增量" + title_add_h_rem)

          // A 高度增量
          var a_h_content = $('.a-title').css('height')
          a_h_content = a_h_content.replace(/px/g, "")
          var a_add_h_super_div_rem = (a_h_content * 2 + 20) / 50
          a_add_h_super_div_rem = a_add_h_super_div_rem - 70 / 50
          console.log("A高度增量" + a_add_h_super_div_rem)
          $('.one-d').css('height', (a_add_h_super_div_rem + 70/50) + 'rem')

          // B 高度增量
          a_h_content = $('.b-title').css('height')
          a_h_content = a_h_content.replace(/px/g, "")
          var b_add_h_super_div_rem = (a_h_content * 2 + 20) / 50
          b_add_h_super_div_rem = b_add_h_super_div_rem - 70 / 50
          console.log("B高度增量" + b_add_h_super_div_rem)
          $('.two-d').css('height', (b_add_h_super_div_rem + 70/50) + 'rem')

          // C 高度增量
          a_h_content = $('.c-title').css('height')
          a_h_content = a_h_content.replace(/px/g, "")
          var c_add_h_super_div_rem = (a_h_content * 2 + 20) / 50
          c_add_h_super_div_rem = c_add_h_super_div_rem - 70 / 50
          console.log("C高度增量" + c_add_h_super_div_rem)
          $('.three-d').css('height', (c_add_h_super_div_rem + 70/50) + 'rem')

          // D 高度增量
          a_h_content = $('.d-title').css('height')
          a_h_content = a_h_content.replace(/px/g, "")
          var d_add_h_super_div_rem = (a_h_content * 2 + 20) / 50
          d_add_h_super_div_rem = d_add_h_super_div_rem - 70 / 50
          console.log("D高度增量" + d_add_h_super_div_rem)
          $('.four-d').css('height', (d_add_h_super_div_rem + 70/50) + 'rem')

          // 总增量 = 题目增量 + A增量 + B增量 + C增量 + D增量
          var total_add_rem = title_add_h_rem + a_add_h_super_div_rem + b_add_h_super_div_rem + c_add_h_super_div_rem + d_add_h_super_div_rem
          
          // 背景图高度 = 原始高度 + 总增量
          var bg_h = 11.2 + total_add_rem
          $('.bg-d').css('height', bg_h + 'rem')

          // 距离top高度
          var mar_top = 5.1 - total_add_rem / 2
          mar_top = mar_top > 0 ? mar_top : 0
          $('.bg-d').css('margin-top', mar_top + 'rem')


        })
      },
      next(item){

        console.log("第" + this.index + "题已答完，共" + this.topicList.length + "题")
        
        if(this.index >= this.topicList.length){

          // 阅读能力
          var correct_rate = (this.correct_count / this.topicList.length * 100).toFixed(0)
          // 阅读速度
          var read_speed = this.wordCount / (this.accum_time / 60)

          console.log("成绩")
          console.log("文章字数：" + this.wordCount + "字")
          console.log("阅读时间：" + this.accum_time + "秒")
          console.log("阅读能力：" + correct_rate + "%")
          console.log("阅读速度：" + read_speed + "字/分钟")

          this.$router.push({
            name:"read_qa_result",
            query:{
              wordCount: this.wordCount,
              read_time: this.read_time,
              accum_time: this.accum_time,
              correct_rate: correct_rate,
              read_speed: read_speed,
              article_id: this.$route.query.article_id,
              topic_count: this.topicList.length,
              correct_count: this.correct_count,
              title: this.title
            }
          })

          return
        }
        this.index ++

        this.cul_height()
      },
      start(){

        var that = this
        this.timer = window.setInterval(function(){

          that.accum_time ++
        }, 1000)
      },
      dealTime(s){

        var min = s / 60
        min = Math.floor(min)
        if(min < 10){
          min = "0" + min
        }
        s -= min * 60
        if(s < 10){
          s = "0" + s
        }
        var st = min + ":" + s
        return st
      }
    }
  }
</script>
<style lang="less" scoped>
  .read_qa{
    overflow: hidden;
    height: 100%;
    header{
      height: 90/50rem;
      border-bottom: 1/50rem solid  #D9D9D9;
      .btn{
        position: absolute;
        img{
          width: 80/50rem;
        }
      }
      .title{
        width: 100%;
        height: 90/50rem;
        color: white;
        position: absolute;
        z-index: 0; 
        background-color: #1B9BFE;
        line-height: 90/50rem;
        text-align: center;
        span{
          font-size: 25/50rem;
        }
      }
      .timer{
        width: 90/50rem;
        height: 80/50rem;
        top: 5/50rem;
        bottom: 5/50rem;
        right: 10/50rem;
        position: absolute;
        span{
          line-height: 80/50rem;
          font-size: 27/50rem;
          color: white;
        }
      }
    }
    .container{
      overflow: hidden;
      height: 100%;
      background: linear-gradient(0.5turn, #8CD5F5, #EBF9FD);
      position: relative;
      .bg-d{
        width: 700/50rem;
        margin: 240/50rem auto 0 auto;
        position: relative;
        .bg-img{
          width: 100%;
          height: 100%;
          position: absolute;
          z-index: 1;
        }
        .qa-content{
          width: 100%;
          position: absolute;
          z-index: 2;
          .qa-title{
            padding-top: 140/50rem;
            padding-left: 130/50rem;
            padding-right: 130/50rem;
            font-weight: bold;
          }
          .one-d, .two-d, .three-d, .four-d{
            height: 70/50rem;
            padding-left: 130/50rem;
            line-height: 50/50rem;
            .a-title, .b-title, .c-title, .d-title{
              width: 370/50rem;
              float: left;
            }
            .check-d{
              width: 50/50rem;
              height: 50/50rem;
              float: right;
              padding-right: 120/50rem;
              img{
                width: 100%;
                vertical-align: middle;
              }
            }
          }
          .one-d{
            margin-top: 30/50rem;
          }
        }
      }
    }
  }
</style>


