<template>
  <div class="read_ready">

    <header>
      <div class="title">{{ title }}<span>（{{ wordCount }}字）</span></div>
      <div class="btn">
        <img @click='back()' src="static/img/header/i-back.png">
      </div>
    </header>

    <div class="container">
      <div class="prompt">倒计时</div>
      <img class="clock" src="static/img/read/i-clock.png">
      <img class="three" :src="'static/img/read/i-t-' + time_img + '.png'">
    </div>
  </div>
</template>
<script type="text/javascript">
import header_index from '../../components/header.vue'
import $ from "jquery"
  export default{
    name:"read_ready",
    data(){
      return{
        title: "",                  // 标题
        thum: "",                   // 略缩图
        articleId: 0,               // 文章id
        wordCount: "",              // 总字数
        timer: "",                  // 定时器
        time_img: "three"           // 倒计时图片
      }
    },
    created(){

      var that = this

      if(this.$route.query.title){
        this.title = this.$route.query.title
      }
      if(this.$route.query.thum){
        this.thum = this.$route.query.thum
      }
      if(this.$route.query.wordCount){
        this.wordCount = this.$route.query.wordCount
      }
      if(this.$route.query.articleId){
        this.articleId = this.$route.query.articleId
      }

      var that = this
      var time = 0
      this.timer = window.setInterval(function(){

        time ++
        if(time == 1){

          that.time_img = "two"
        }
        if(time == 2){

          that.time_img = "one"
        }
        if(time == 3){

          window.clearInterval(that.timer)

          that.$router.push({
            name:"read_content",
            query:{
              title: that.title,
              thum: that.thum,
              articleId: that.$route.query.articleId,
              wordCount: that.$route.query.wordCount,
              word_per_minute: that.$route.query.word_per_minute,
              word_per_line: that.$route.query.word_per_line
            }
          })
        }
      }, 1000)
    },
    beforeDestroy(){

      console.log("对象准备死掉")
      // 去除定时器
      window.clearInterval(this.timer)
    },
    components:{
      header_index,
    },
    methods:{
      back(){

        this.$router.push({
          name:"read_speed",
          query:{
            title: this.title,
            thum: "thum-book3.png",
            articleId: this.articleId,
            wordCount: this.wordCount,
            word_per_minute: this.$route.query.word_per_minute,
            word_per_line: this.$route.query.word_per_line
          }
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  header{
    height: 90/50rem;
    border-bottom: 1/50rem solid  #D9D9D9;
    .btn{
      img{
        width: 80/50rem;
      }
    }
    .title{
      width: 100%;
      height: 90/50rem;
      color: white;
      position: absolute;
      z-index: -1;
      background-color: #1B9BFE;
      line-height: 90/50rem;
      text-align: center;
      span{
        font-size: 25/50rem;
      }
    }
  }
  .read_ready{
    overflow: hidden;
    .container{
      overflow: hidden;
      text-align: center;
      .prompt{
        margin-top: 70/50rem;
        font-weight: bold;
      }
      .clock{
        width: 160/50rem;
        height: 180/50rem;
        display: block;
        margin: 45/50rem auto 0 auto;
      }
      .three{
        width: 160/50rem;
        height: 180/50rem;
        display: block;
        margin: 180/50rem auto 0 auto;
      }
    }
  }
</style>


