<template>
  <div class="login_cellphone">
    
    <div class="container">
		  <div class="logo">
        <div class="logo-img"><img src="static/img/login/logo.png"></div>
        <div class="logo-text">新用户登录即可享受3天VIP</div>
      </div>
      <div class="i-iphone">
        <div>
          <div><div><img src="static/img/login/i-input-triangle.png"></div><span>+86</span></div>
          <div><input v-model="cellphone" placeholder='请输入手机号'></div>
        </div>
        <div>
          <div><img src="static/img/login/i-input-maill.png"></div>
          <div><input v-model="verificationCode" placeholder='短信验证码'>
            <div class="verCode" @click='getVerificationCode()'>获取动态码</div>
          </div>
        </div>
      </div>
      <div class="protocol">
        <div class="protocol-img" @click='protocol_agree()'><img class="protocol-img-" src="static/img/login/i-protocol-yes.png"></div>
        <div class="protocol-text"><span>登录即同意</span><span  @click='protocol_content()'>《高效速读》用户协议</span></div>
      </div>
      <div class="btn" @click='login()'>登录</div>
      <div class="look" @click='look()'>随便看看</div>
      <div class="wechat-d" @click='wechat()'>
        <img src="static/img/login/wechat.png">
        <div class="wechat-text">微信登录</div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
import $ from "jquery"
  export default{
    name:"login_cellphone",
    data(){
      return{
        cellphone: "",               //手机号
        verificationCode: "",        //验证码
        verCode_click_able: true,    //验证码按钮是否可用
        timer: "",                   //定时器
        verCode_cd: 60,               //倒计时
        protocol: true,              //是否选中协议 
      }
    },
    created(){

      // this.cellphone = "13726027405"

      var that = this

      // 有token，表示退出到此页面
      if(that.$store.state.token.length > 0){
        try{
          plus.plugintest.PluginGetUserFunction("", function( result ){
            return
          }, function(result){
            var user_info = JSON.parse(result[0])
            that.cellphone = user_info.phone
            return
          })
        }catch(error){ }
      }else{
        try{
          plus.plugintest.PluginGetUserFunction("", function( result ){
            return
          }, function(result){
            var user_info = JSON.parse(result[0])
            that.$store.state.login_cellphone = user_info
            that.$store.state.token = user_info.token
            if(user_info.token.length > 0){
              that.$router.push({
                name:"index",
              })
            }
            return
          })
        }catch(error){ }
      }
    },
    methods:{
      getVerificationCode(){

        var that = this

        if(this.cellphone == ""){
          this.$toast({message:"手机号不能为空", type:'', position:'bottom', duration:'2000'})
          return
        }

        if(this.verCode_click_able == false){
          return
        }

        var postData = {
          "cellphone": this.cellphone, //手机号
        }
        this.httpRequest("user/cellphone/sendCellphoneYzm", postData, function(res){

          that.$toast({message:"发送成功", type:'', position:'bottom', duration:'2000'})
        })

        this.verCode_cd = 60
        this.timer = window.setInterval(function(){

          that.verCode_cd --
          if(that.verCode_cd == 0){
            window.clearInterval(that.timer)
            $(".verCode").html("获取动态码")
            $(".verCode").css("color", "#4EA3FE")
            that.verCode_click_able = true
          }else{
            $(".verCode").html(that.verCode_cd + "S")
            $(".verCode").css("color", "#999999")
            that.verCode_click_able = false
          }
        }, 1000)

      },
      protocol_agree(){

        if(this.protocol == true){
          $(".protocol-img-").attr("src", "static/img/login/i-protocol-no.png")
          this.protocol = false
        }else{
          $(".protocol-img-").attr("src", "static/img/login/i-protocol-yes.png")
          this.protocol = true
        }
      },
      protocol_content(){

        this.$router.push("protocol")
      },
      login(){

        if(this.protocol == false){
          this.$toast({message:"请同意《高效速读》用户协议", type:'', position:'bottom', duration:'2000'})
          return
        }
        if(this.cellphone == ""){
          this.$toast({message:"手机号不能为空", type:'', position:'bottom', duration:'2000'})
          return
        }
        if(this.verificationCode == ""){
          this.$toast({message:"验证码不能为空", type:'', position:'bottom', duration:'2000'})
          return
        }

        var that = this

        var postData = {
          "cellphone": this.cellphone, //手机号
          "APPLOGIN": "T",
          "yzm": this.verificationCode,
        }
        this.httpRequest_login("user/login", postData, function(res){

          var user_info = JSON.stringify(res.data)

          try{
            plus.plugintest.PluginPrintIosFunction(user_info, function( result ){
              return
            }, function(result){
              return
            })
          }catch(error){ }

          that.$store.state.login_cellphone = res.data
          that.$store.state.token = res.data.token
          // that.set_token(res.data.token)

          that.$router.push({
            name:"index",
          })
        })
      },
      wechat(){

        this.$toast({message:"维护中...", type:'', position:'bottom', duration:'3000'})
      }
    }
  }
</script>
<style lang="less" scoped>
  .login_cellphone{
    overflow: hidden;
    .container{
      overflow: hidden;
      padding-left: 60/50rem;
      margin-right: 60/50rem;
      .logo{
        margin-top: 100/50rem;
        .logo-img{
          width: 170/50rem;
          height: 200/50rem;
          margin: 0 auto;
          img{
            height: 100%;
          }
        }
        .logo-text{
          width: 350/50rem;
          margin: 0 auto;
          color: #999999;
          margin-top: 20/50rem;
          font-size: 28/50rem;
        }
      }
      .i-iphone{
        margin-top: 100/50rem;
        &>div{
          border-bottom: 1/50rem solid  #999999;
          input{
            outline: none;
            background: transparent;
            border: none;
            height: 100%;
            width: 100%;
          }
          input::-webkit-input-placeholder {
            font-size: 32/50rem;
            color: #999999;
          }
          input::-moz-input-placeholder {
            font-size: 32/50rem;
            color: #999999;
          }
          input::-ms-input-placeholder {
            font-size: 32/50rem;
            color: #999999;
          }
          &:nth-child(1){
            height: 80/50rem;
            &>div{
              overflow: hidden;
              height: 100%;
              &:nth-child(1){
                font-size: 32/50rem;
                width: 22%;
                float: left;
                line-height: 80/50rem;
                span{
                  float: right;
                  margin-right: 7/50rem;
                  color: #666666;
                }
                &>div{
                  width: 30/50rem;
                  height: 80%;
                  float: right;
                  text-align: center;
                  margin-right: 8/50rem;
                  img{
                    width: 15/50rem;
                    vertical-align: middle;
                  }
                }
              }
              &:nth-child(2){
                float: left;
                width: 78%;
              }
            }
          }
          &:nth-child(2){
            margin-top: 40/50rem;
            height: 80/50rem;
            &>div{
              overflow: hidden;
              height: 100%;
              &:nth-child(1){
                width: 22%;
                float: left;
                line-height: 60/50rem;
                text-align: center;
                img{
                  width: 40/50rem;
                  vertical-align: middle;
                  margin-left: 40/50rem;
                }
              }
              &:nth-child(2){
                float: left;
                width: 78%;
                input{
                  float: left;
                  width: 65%;
                }
                &>div{
                  font-size: 32/50rem;
                  height: 100%;
                  float: left;
                  width: 35%;
                  line-height: 80/50rem;
                  color: #4EA3FE;
                }
              }
            }
          }
          .verCode{
            text-align: center;
          }
        }
      }
      .btn{
        margin-top: 80/50rem;
        height: 96/50rem;
        background-color: red;
        border-radius: 149/50rem;
        line-height: 96/50rem;
        text-align: center;
        color: white;
        background: linear-gradient(0.25turn, #99cafb, #4da4fa);
        font-size: 32/50rem;
      }
      .look{
        width: 130/50rem;
        margin: 0 auto;
        margin-top: 15/50rem;
        font-size: 30/50rem;
        padding: 30/50rem;
        color: #1B9BFE;
      }
      .wechat-d{
        width: 116/50rem;
        height: 130/50rem;
        margin: 30/50rem auto 0 auto;
        padding: 20/50rem;
        img{
          width: 80%;
          margin-left: 12/50rem;
        }
        .wechat-text{
          font-size: 28/50rem;
        }
      }
      .protocol{
        width: (750 - 60 - 60)/50rem;
        height: 50/50rem;
        margin-top: 30/50rem;
        .protocol-img{
          width: 50/50rem;
          height: 100%;
          float: left;
          img{
            width: 70%;
            vertical-align: middle;
          }
        }
        .protocol-text{
          width: 80%;
          height: 100%;
          font-size: 24/50rem;
          line-height: 50/50rem;
          float: left;
          &>span:nth-child(1){
            color: #999999;
          }
          &>span:nth-child(2){
            color: #1B9BFE;
          }
        }
      }
    }
  }
</style>


