import Vue from "vue"
import vuex from "vuex"

Vue.use(vuex);

export default new vuex.Store({
	state:{

		token:"",

		// 用户信息
		login_cellphone:{},

		// 消息列表
		msg_list:[],

		// 文章列表
		read_list:[],

		// 请求超时列表
		request_timed_out_list:[],

		// type: 阅读模式 单篇阅读/单篇检测
		record_read: {id:"",type:"",start_time:"",end_time:""},

        // 阅读速度
		read_speed_and_range: {speed:200,range:0},

		// 课内/课外
		books: {class_index:2,type:"课内"}
	}
})


