<template>
  <div class="recom_books">

    <header_index title='好书推荐' :back_icon='true' @back='back'></header_index>

    <div class="container">

        <div v-for='(item, index) in read_list' :id="index" :key='index' class="v-f-read" @click='toIntrodu(item.title, item.id, item.wordCount)'>
          <div class="thum"><img src="static/img/index/i-book-thum.png"></div>
          <div class="content">
            <div>{{ item.title }}</div>
            <div>分类：单篇阅读</div>
          </div>
        </div>

    </div>
  </div>
</template>
<script type="text/javascript">
import header_index from '../../components/header.vue'
  export default{
    name:"recom_books",
    data(){
      return{
        read_list: []
      }
    },
    components:{
      header_index
    },
    created(){

      this.request()
    },
    methods:{

      back(){

        this.$router.push("index")
      },
      request(){

        var that = this

        var postData = {
          "currentPage": 0,
          "pageSize": 20
        }
        this.httpRequest_token("read/api/v1/appRead/getArticleSingleList", postData, function(res){

          that.read_list = res.data
          that.$store.state.read_list = that.read_list
          
          if(that.read_list.length == 0){
            that.no_data_prompt = "无数据"
          }

          // 删除重新请求队列
          let index = that.$store.state.request_timed_out_list.indexOf('read/api/v1/appRead/getArticleSingleList')
          if(index != -1){
            that.$store.state.request_timed_out_list.splice(index, 1)
          }
        })
      },
      toIntrodu(title, articleId, wordCount){
        this.$router.push({
          name:"read_single",
          query:{
            title: title,
            thum: "thum-book3.png",
            articleId: articleId,
            wordCount: wordCount
          }
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  .recom_books{
    overflow: hidden;
    height: 100%;
    .container{
      height: calc(100% - 1.8rem);
      overflow: scroll;
      .v-f-read{
        padding-left: 28/50rem;
        padding-top: 20/50rem;
        padding-bottom: 20/50rem;
        height: 180/50rem;
        border-bottom: 1/50rem solid  #999999;
        .thum{
          width: 270/50rem;
          height: 180/50rem;
          float: left;
          img{
            width: 100%;
          }
        }
        .content{
          width: calc(100% - 6.26rem);
          height: 180/50rem;
          float: right;
          &>div{
            width: 100%;
            height: 50%;
            &:nth-child(1){
              font-size: 35/50rem;
              font-weight: bold;
              line-height: 110/50rem;
            }
            &:nth-child(2){
              font-size: 28/50rem;
              color: #818080;
              line-height: 70/50rem;
            }
          }
        }
      }
    }
  }
</style>


