<template>
  <div class="my">
    
    <div class="container">
		  <div class="bg">
        <img src="static/img/my/my-bg.png">
      </div>
      <div class="top">
        <div class="d-adv">
          <img class="i-adv" src="static/img/my/i-adv.png">
          <img class="i-vip" src="static/img/my/i-vip.png">
        </div>
        <div class="idx">ID：{{ $store.state.login_cellphone.id }}</div>
        <div class="tel">{{ $store.state.login_cellphone.phone }}</div>
        <div class="user-sex">高效速读用户<img class="i-vip" src="static/img/my/i-sex.png"></div>
      </div>

      <div class="mid">
        <div>
          <div><img src="static/img/my/i-gift.png"></div>
          <div><p>邀请有礼</p><p>得7天VIP</p></div>
        </div>
        <div><div class="fds"></div></div>
        <div>
          <div><img src="static/img/my/i-badge.png"></div>
          <div><p>VIP续费</p><p>2020-03-01到期</p></div>
        </div>
      </div>

      <div class="bottom">

        <div>
          <div class="left">
            <img src="static/img/my/i-invitation-recordr.png">
          </div>
          <div class="right">
            <span>邀请记录</span>
            <img src="static/img/my/i-push.png">
          </div>
        </div>

        <div @click="to_personal_info()">
          <div class="left">
            <img src="static/img/my/i-my-account.png">
          </div>
          <div class="right">
            <span>我的账户</span>
            <img src="static/img/my/i-push.png">
          </div>
        </div>

        <div>
          <div class="left">
            <img src="static/img/my/i-money.png">
          </div>
          <div class="right">
            <span>已购买订单</span>
            <img src="static/img/my/i-push.png">
          </div>
        </div>

        <div>
          <div class="left">
            <img src="static/img/my/i-offline-courses.png">
          </div>
          <div class="right">
            <span>线下课程</span>
            <img src="static/img/my/i-push.png">
          </div>
        </div>

        <div @click="to_setting()">
          <div class="left">
            <img src="static/img/my/i-setting.png">
          </div>
          <div class="right">
            <span>设置</span>
            <img src="static/img/my/i-push.png">
          </div>
        </div>

      </div>

    </div>
    <footer_index/>
  </div>
</template>
<script type="text/javascript">
import footer_index from '../../components/footer.vue'
  export default{
    name:"my",
    data(){
      return{

      }
    },
    components:{
      footer_index
    },
    created(){

    },
    methods:{
      to_personal_info(){

        this.$router.push("personal_info")
      },
      to_setting(){

        this.$router.push("setting")
      }
    }
  }
</script>
<style lang="less" scoped>
  .my{
    height: 100%;
    overflow: hidden;
    .container{
      height: 100%;
      overflow: hidden;
      background: linear-gradient(0.5turn, #8CD5F5, #EBF9FD);
      position: relative;
      z-index: 0;
      .bg{
        height: 432/50rem;
        width: 100%;
        position: fixed;
        z-index: -1;
        background-color: white;
        img{
          width: 100%;
          height: 230/50rem;
        }
      }
      .top{
        width: 100%;
        height: 332/50rem;
        margin-top: 100/50rem;
        .d-adv{
          width: 200/50rem;
          height: 200/50rem;
          margin: 0 auto;
          position: relative;
          .i-adv{
            width: 200/50rem;
            position: absolute;
          }
          .i-vip{
            width: 50/50rem;
            position: absolute;
            right: 20/50rem;
            bottom: 0;
          }
        }
        .idx{
          margin-top: -120/50rem;
          margin-right: 27/50rem;
          float: right;
          color: white;
          font-size: 24/50rem;
        }
        .tel{
          width: 100%;
          height: 50/50rem;
          line-height: 50/50rem;
          color: #333333;
          text-align: center;
          margin-top: 7/50rem;
          font-size: 29/50rem;
        }
        .user-sex{
          width: 100%;
          height: 50/50rem;
          line-height: 50/50rem;
          text-align: center;
          color: #333333;
          font-size: 29/50rem;
          img{
            height: 80%;
            vertical-align: middle;
            margin-left: 10/50rem;
            margin-top: -6/50rem;
          }
        }
      }
      .mid{
        height: 140/50rem;
        margin-top: 24/50rem;
        background: rgba(255, 255, 255, 0.85);
        &>div{
          height: 100%;
          line-height: 136/50rem;
          &:nth-child(1){
            float: left;
            width: 49%;
          }
          &:nth-child(2){
            float: left;
            width: 2%;
            &>div{           
              width: 1/50rem;
              height: 100%;
              background-color: #DDDDDD;
              position: relative;
            }
          }
          &:nth-child(3){
            float: right;
            width: 49%;
          }
          &>div:not(.fds){
            height: 100%;
            &:nth-child(1){
              float: left;
              width: (25 + 78 + 25)/50rem;
              img{
                margin-left: 25/50rem;
                width: 78/50rem;
                vertical-align: middle;
              }
            }
            &:nth-child(2){
              float: left;
              p{
                line-height:  37/50rem;
                &:nth-child(1){
                  padding-top: 34/50rem;
                  font-size: 40/60rem;
                  font-weight: bold;
                }
                &:nth-child(2){
                  margin-top: 9/50rem;
                  font-size: 30/60rem;
                  color: #666666;
                }
              }
            }
          }
        }
      }
      .bottom{
        margin-top: 25/50rem;
        background: rgba(255, 255, 255, 0.85);
        &>div{
          width: 100%;
          height: 90/50rem;
          .left{
            width: 120/50rem;
            height: 100%;
            float: left;
            text-align: center;
            img{
              width: 50/50rem;
              height: 50/50rem;
              margin-top: 25/50rem;
            }
          }
          .right{
            width: (750 - 120)/50rem;
            height: 100%;
            float: right;
            border-bottom: 1/50rem solid #DDDDDD;
            line-height: 90/50rem;
            span{
              width: 200/50rem;
            }
            img{
              height: 100%;
              float: right;
              margin-right: 40/50rem;
            }
          }
        }
      }
    }
  }
</style>


