<template>
  <div class="class_join">

    <header_index title="加入班级" :back_icon='true' @back='back'></header_index>

    <div class="container">

      <div class="title-">加入一个班级</div>

      <div class="input-">
        <div><input placeholder='请输入老师给您的班级号'></div>
      </div>

      <div class="prompt-">如何获取班级号?</div>

      <div class="btn" @click='search()'>查找班级</div>

    </div>
  </div>
</template>
<script type="text/javascript">
import header_index from '../../components/header.vue'
  export default{
    name:"class_join",
    data(){
      return{

      }
    },
    components:{
      header_index,
    },
    created(){
    },
    methods:{
      back(){
        this.$router.push({
          name:"class",
        })
      },
      search(){
        
      }
    }
  }
</script>
<style lang="less" scoped>
  .class_join{
    overflow: hidden;
    .container{
      overflow: hidden;
      margin-left: 32/50rem;
      margin-right: 32/50rem;
      .title-{
        margin-top: 45/50rem;
        margin-left: 32/50rem;
      }
      .input-{
        margin-top: 45/50rem;
        height: 80/50rem;
        border-bottom: 1/50rem solid  #999999;
        input{
          outline: none;
          background: transparent;
          border: none;
          height: 80/50rem;
          width: 100%;
          position: relative;
          left: 32/50rem;
        }
        input::-webkit-input-placeholder {
          font-size: 30/50rem;
          color: #999999;
        }
        input::-moz-input-placeholder {
          font-size: 30/50rem;
          color: #999999;
        }
        input::-ms-input-placeholder {
          font-size: 30/50rem;
          color: #999999;
        }
      }
      .prompt-{
        margin-top: 20/50rem;
        font-size: 22/50rem;
        color: #FF3B30;
        position: relative;
        left: 32/50rem;
      }
      .btn{
        margin-top: 80/50rem;
        height: 96/50rem;
        background-color: red;
        border-radius: 149/50rem;
        line-height: 96/50rem;
        text-align: center;
        color: white;
        background: linear-gradient(0.25turn, #99cafb, #4da4fa);
        font-size: 32/50rem;
      }
    }
  }
</style>


