<template>
  <div class="book_introdu">

      <header>
        <img @click="back" src="static/img/class/i-back.png">
        {{ title }}
        <span>({{ wordCount }}字)</span>
      </header>

    <div class="container">

      <div class="img">
        <img :src="'static/img/read/' + thum">
      </div>

      <div class="title">{{ title }}</div>

      <div class="curr-read-speed">当前速度：{{ word_per_minute }}字/分钟</div>

      <div class="min_max_speed">
        <div class="del" @click="del('min')">-</div>
        <div class="curr-speed">{{ word_per_minute }}字/分钟</div>
        <div class="add" @click="add('min')">+</div>
      </div>

      <div class="min_max_speed">
        <div class="del" @click="del('line')">-</div>
        <div class="curr-speed">{{ word_per_line }}字/行</div>
        <div class="add" @click="add('line')">+</div>
      </div>

      <div class="btn" @click="next()">定速阅读</div>

      <div class="btn-test" @click="next_test()">阅读测试</div>

    </div>
  </div>
</template>
<script type="text/javascript">
import header_index from '../../components/header.vue'
import $ from "jquery"
  export default{
    name:"book_introdu",
    data(){
      return{
        title: "",                  // 标题
        books_selected_class: "",   // 几年级的书
        thum: "",                   // 略缩图
        read_single_title: "",      // 标题
        wordCount: "",              // 总字数
        word_per_minute: 2400,      // 字/分钟
        word_per_line: 12,          // 字/行
      }
    },
    components:{
      header_index,
    },
    created(){

      if(this.$route.query.title){
        this.title = this.$route.query.title
      }
      if(this.$route.query.books_selected_class){
        this.books_selected_class = this.$route.query.books_selected_class
      }
      if(this.$route.query.thum){
        this.thum = this.$route.query.thum
      }
      if(this.$route.query.read_single_title){
        this.read_single_title = this.$route.query.read_single_title
      }
      if(this.$route.query.wordCount){
        this.wordCount = this.$route.query.wordCount
      }

      var postData = {
        "speed": this.wordCount
      }

      this.httpRequest_token("read/api/v1/appRead/getSpeedWordNumber", postData, function(res){

      })
    },
    methods:{
      back(){
        this.$router.push({
          name:"recom_books"
        })
      },
      next(){

        this.$router.push({
          name:"read_content",
          query:{
            title: this.title,
            books_selected_class: this.books_selected_class,
            thum: this.thum,
            read_single_title: this.$route.query.read_single_title,
            articleId: this.$route.query.articleId,
            wordCount: this.wordCount,
            word_per_minute: this.word_per_minute,
            word_per_line: this.word_per_line
          }
        })
      },
      del(type){

        if(type == "min"){
          this.word_per_minute -= 200
        }else{
          this.word_per_line -= 1;
        }
      },
      add(type){

        if(type == "min"){
          this.word_per_minute += 200
        }else{
          this.word_per_line += 1;
        }
      },
      next_test(){

        this.$router.push({
          name:"read_content_qa",
          query:{
            title: this.title,
            books_selected_class: this.books_selected_class,
            thum: this.thum,
            read_single_title: this.$route.query.read_single_title,
            articleId: this.$route.query.articleId,
            wordCount: this.wordCount
          }
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  .book_introdu{
    height: 100%;
    overflow: hidden;
    header{
      height: 90/50rem;
      font-weight: 550;
      font-size: 36/50rem;
      color: 333333;
      border-bottom: 1/50rem solid #D9D9D9;
      img{
        width: 80/50rem;
        vertical-align: middle;
      }
      span{
        font-size: 24/50rem;
        color: #666666;
      }
    }
    .container{
      height: 100%;
      overflow: hidden;
      margin-left: 32/50rem;
      margin-right: 32/50rem;
      text-align: center;
      .img{
        overflow: hidden;
        width: 330/50rem;
        height: 400/50rem;
        margin-top: 100/50rem;
        background-color: white;
        display: inline-block;
        img{
          width:100%;
          height:100%;
          display:block;
          vertical-align: middle;
        }
      }
      .title{
        margin-top: 32/50rem;
        font-size: 48/50rem;
        font-weight: 600;
      }
      .curr-read-speed{
        margin-top: 90/50rem;
        font-size: 48/50rem;
      }
      .min_max_speed{
        width: 350/50rem;
        height: 80/50rem;
        margin: 45/50rem auto;
        border: 2/50rem solid  #D9D9D9;
        &>div{
          text-align: center;
        }
        .del{
          width: 75/50rem;
          height: 100%;
          float: left;
          background-color: #4373B3;
          line-height: 80/50rem;
        }
        .curr-speed{
          width: 200/50rem;
          height: 100%;
          font-size: 25/50rem;
          float: left;
          line-height: 80/50rem;
          background-color: #FEF4C8;
        }
        .add{
          width: 75/50rem;
          height: 100%;
          float: right;
          background-color: #4373B3;
          line-height: 80/50rem;
        }
      }
      .btn{
        height: 80/50rem;
        margin-top: -22/50rem;
        margin-left: (166 - 32)/50rem;
        margin-right: (166 - 32)/50rem;
        font-size: 48/50rem;
        font-weight: 600;
        color: white;
        line-height: 75/50rem;
        border-radius: 40/50rem;
        background: linear-gradient(0.25turn, #99cafb, #4da4fa);
      }
      .btn-test{
        height: 80/50rem;
        margin-top: 20/50rem;
        margin-left: (166 - 32)/50rem;
        margin-right: (166 - 32)/50rem;
        font-size: 48/50rem;
        font-weight: 600;
        color: white;
        line-height: 75/50rem;
        border-radius: 40/50rem;
        background: linear-gradient(0.25turn, #99cafb, #4da4fa);
      }
    }
  }
</style>


