<template>
  <div class="class_create">

    <header_index title="创建班级" :back_icon='true' @back='back'></header_index>

    <div class="container">

      <div class="input-div">
        <div>
          <div class="prompt">班级名称</div>
        </div>
        <div>
          <input placeholder='请输入班级名称'>
        </div>
      </div>

      <div class="select-div">
        <div></div>
        <div>
          <input placeholder='语文'>
          <div class="select-btn"><img src="static/img/class/i-select.png"></div>
        </div>
      </div>

      <div class="input-div">
        <div>
          <div class="prompt">所属学校</div>
        </div>
        <div>
          <input placeholder='深圳幼儿大学'>
        </div>
      </div>

      <div class="input-div">
        <div>
          <div class="prompt">所属班级</div>
        </div>
        <div>
          <input placeholder='请输入班级名称'>
        </div>
      </div>

    </div>
  </div>
</template>
<script type="text/javascript">
import header_index from '../../components/header.vue'
  export default{
    name:"class_create",
    data(){
      return{

      }
    },
    components:{
      header_index,
    },
    created(){
    },
    methods:{
      back(){
        this.$router.push({
          name:"class",
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  .class_create{
    overflow: hidden;
    .container{
      overflow: hidden;
      margin-left: 32/50rem;
      margin-right: 32/50rem;
      .input-div{
        margin-top: 45/50rem;
        height: 85/50rem;
        overflow: hidden;
        &>div{
          &:nth-child(1){
            height: 15/50rem;
            position: relative;
            left: 40/50rem;
            .prompt{
              height: 30/50rem;
              background-color: white;
              float: left;
              font-size: 24/50rem;
              color: #999999;
              padding-left: 8/50rem;
              padding-right: 8/50rem;
            }
          }
          &:nth-child(2){
            height: 68/50rem;
            border-radius: 70/50rem;
            border: 1/50rem solid #CCCCCC;
            input{
              float: left;
              width: 100%;
              height: 100%;
              background-color: red;
              position: relative;
              left: 40/50rem;
              top: -15/50rem;
              outline:none;
              background:transparent;
              border:none;
            }
            input::-webkit-input-placeholder {
              font-size: 30/50rem;
              color: #999999;
            }
            input::-moz-input-placeholder {
              font-size: 30/50rem;
              color: #999999;
            }
            input::-ms-input-placeholder {
              font-size: 30/50rem;
              color: #999999;
            }
          }
        }
      }
      .select-div{
        margin-top: 45/50rem;
        height: 85/50rem;
        overflow: hidden;
        &>div{
          &:nth-child(1){
            height: 15/50rem;
          }
          &:nth-child(2){
            height: 68/50rem;
            border-radius: 70/50rem;
            border: 1/50rem solid #CCCCCC;
            input{
              float: left;
              width: calc(100% - 2.72rem);
              height: 100%;
              background-color: red;
              position: relative;
              left: 40/50rem;
              outline: none;
              background: transparent;
              border: none;
            }
            input::-webkit-input-placeholder {
              font-size: 30/50rem;
              color: #999999;
            }
            input::-moz-input-placeholder {
              font-size: 30/50rem;
              color: #999999;
            }
            input::-ms-input-placeholder {
              font-size: 30/50rem;
              color: #999999;
            }
            .select-btn{
              float: left;
              height: 100%;
              width: 96/50rem;
              position: relative;
              left: 40/50rem;
              text-align: center;
              img{
                width: 32/50rem;
                vertical-align: middle;
                position: relative;
                top: 7.5/50rem;
              }
            }
          }
        }
      }
    }
  }
</style>


