<template>
  <div class="login">
    <div class="container">
		  <div class="text"><img src="static/img/login/login-text.png"></div>
      <div class="img"><img src="static/img/login/login-read.jpg"></div>
      <div class="btn">
        <div class="wechat" @click='wechat()'>微信登录</div>
        <div class="cellphone" @click='cellphone()'>手机号登录</div>
      </div>
      <div class="look" @click='look()'>随便看看</div>
    </div>
  </div>
</template>
<script type="text/javascript">
  export default{
    name:"login",
    data(){
      return{

      }
    },
    created(){

      this.$store.state.request_timed_out_list = []
    },
    methods:{
      wechat(){

        this.$toast({message:"维护中...", type:'', position:'bottom', duration:'3000'})
      },
      cellphone(){

        this.$router.push("login_cellphone")
      },
      look(){

        this.$toast({message:"维护中...", type:'', position:'bottom', duration:'3000'})
      }
    }
  }
</script>
<style lang="less" scoped>
  .login{
    overflow: hidden;
    .container{
      overflow: hidden;
      .text{
        width: 100%;
        margin-top: 70/50rem;
        margin-left: 15/50rem;
        img{
          width: 80%;
        }
      }
      .img{
        width: 550/50rem;
        height: 580/50rem;
        margin: 0 auto;
        background-color: white;
        margin-top: 30/50rem;
        img{
          width: 100%;
        }
      }
      .btn{
        &>div{
          width: 450/50rem;
          height: 75/50rem;
          margin: 0 auto;
          border-radius: 8/50rem;
          color: white;
          text-align: center;
          line-height: 75/50rem;
          font-size: 37/50rem;
          border: 1/50rem solid #CCCCCC;
        }
        .wechat{
          background-color: #00af50;
          margin-top: 200/50rem;
        }
        .cellphone{
          background-color: white;
          margin-top: 50/50rem;
          color: black;
        }
      }
      .look{
        width: 110/50rem;
        margin: 0 auto;
        margin-top: 15/50rem;
        font-size: 26/50rem;
        padding: 30/50rem;
      }
    }
  }
</style>


