<template>
  <div class="read_content">
      
    <div class="cover-">
      <div class="bg-container">
        <img src="static/img/read/i-read-end-bg.png">
        <span class="one">闭目回想</span>
        <span class="two">写题纲</span>
        <div class="agin" @click='agin_click()'>再读一遍</div>
        <div class="back-" @click='back_()'>返回</div>
      </div>
    </div>

    <div class="cove-stop">
      <div class="bg-container">
        <img src="static/img/read/i-stop-.png">
        <div class="cove-t">已暂停</div>
      </div>
    </div>

    <header>
      <div class="title">
        {{ title }}<span>（{{ $route.query.wordCount }}字）</span>
        <div class="stop" @click="stop_click">暂停</div>
      </div>
      <div class="btn">
        <img @click='back()' src="static/img/header/i-back.png">
      </div>
    </header>

    <div class="container">

      <div class="cover"><img class="cover-bg" src="static/img/read/read-cover.png"></div>

      <div class="v-for" v-for='(item, index) in pages[curr_page - 1]' :id="index" :key='index'>
        {{ item }}
      </div>

    </div>
  </div>
</template>
<script type="text/javascript">
import header_index from '../../components/header.vue'
import $ from "jquery"
  export default{
    name:"read_content",
    data(){
      return{
        title: "",
        books_selected_class: "",
        thum: "",                 //略缩图
        read_single_title:"",     //标题
        data: {"content":""},     //数据
        lookTime: 0,
        timer: "",                //定时器
        screenWidth: 0,           //屏幕宽度
        screenHeight: 0,          //屏幕高度
        pages: [],                //处理后数据
        maxLine: 0,               //每页行数
        word_per_line: 0,         //多少字/行
        word_per_minute: 0,       //多少字/分钟
        total_time: 0,            //总用时(秒)
        accum_time: 0,            //累计用时(秒)
        second_to_refresh: 1,     //多少秒刷新一次
        total_page: 0,            //总页数
        curr_page: 1,             //当前页
        setTimeout: "",           //延迟
        stop_btn_able: false,     //暂停按钮是否可用
        screen_sr: 1,             //屏幕尺寸标准比，当前设备宽度/375，例如苹果5s，320/375
        content_h: 0,             //显示文章内容的高度
        blank_qty: 0,             //补充空格数
        content_sr: 1             //文章长度标准比，处理前文章长度
      }
    },
    components:{
      header_index,
    },
    beforeMount() {

      console.log("life-beforeMount")
 
      this.screenWidth = document.documentElement.clientWidth || document.body.clientWidth
      this.screenHeight = document.documentElement.clientHeight || document.body.clientHeight
      this.screen_sr = this.screenWidth / 375
      var line_h = 30 * this.screen_sr
      console.log("屏幕宽：" + this.screenWidth)
      console.log("屏幕高：" + this.screenHeight)
      console.log("标准比：" + this.screen_sr)

      this.content_h = this.screenHeight - 45 * this.screen_sr
      console.log("文章容器高：" + this.content_h)

      this.maxLine = this.content_h / line_h
      this.maxLine = Math.floor(this.maxLine)
      console.log("一行高度" + line_h + "，能显示：" + this.maxLine + "行" + "   余：" + (this.content_h - this.maxLine * line_h))

      // 此行出现过两次，网络耗时和生命周期，不能确保执行顺序，所以执行两行
      this.second_to_refresh = (this.word_per_line * this.maxLine * 60) / this.word_per_minute / 100 / this.content_sr
    },
    created(){

      console.log("life-created")

      var that = this

      this.$store.state.record_read.start_time = this.getNowTime()

      if(this.$route.query.title){
        this.title = this.$route.query.title
      }
      if(this.$route.query.books_selected_class){
        this.books_selected_class = this.$route.query.books_selected_class
      }
      if(this.$route.query.thum){
        this.thum = this.$route.query.thum
      }
      if(this.$route.query.read_single_title){
        this.read_single_title = this.$route.query.read_single_title
      }
      if(this.$route.query.word_per_minute){
        this.word_per_minute = this.$route.query.word_per_minute
      }
      if(this.$route.query.word_per_line){
        this.word_per_line = this.$route.query.word_per_line
      }

      console.log(that.data)

      if(that.data.content.length == 0){

        var postData = {
          "articleId": this.$route.query.articleId
        }
        this.httpRequest_token("read/api/v1/appRead/getArticleById", postData, function(res){

          console.log("life-http")

          that.data = res.data
          
          var deal = that.delHtmlTag(that.data.content)
          var count = deal.length
          var lineCount = count / that.word_per_line
          // 行数向上取整，66.1算67行
          lineCount = Math.ceil(lineCount)

          // 多少页
          that.total_page = lineCount / that.maxLine
          that.total_page = Math.ceil(that.total_page)

          console.log("总字数：" + count + "   行数：" + lineCount + "   页数：" + that.total_page)

          for(var i = 0; i < that.total_page; i++){

            var lines = []
            for(var j = 0; j < that.maxLine; j++){

              var line = deal.substring(j * that.word_per_line + ((i * that.maxLine) * that.word_per_line), (j + 1) * that.word_per_line + ((i * that.maxLine) * that.word_per_line))
              lines.push(line)
            }
            that.pages.push(lines)
          }
          console.log(that.pages)

          that.total_time = count / that.word_per_minute * 60 / that.content_sr

          console.log("总字数：" + count + "   速度：" + that.word_per_minute + "   时间：" + that.total_time + "秒")
          console.log("补充空格数" + that.blank_qty)
          // 此行出现过两次，网络耗时和生命周期，不能确保执行顺序，所以执行两行
          that.second_to_refresh = (that.word_per_line * that.maxLine * 60) / that.word_per_minute / 100 / that.content_sr

          that.setTimeout = setTimeout(function () {
            that.stop_btn_able = true
            that.start()
          }, 2000)
          that.uploadData(true, "F")
        })
      }
    },
    beforeDestroy(){

      console.log("对象准备死掉")
      // 去除定时器
      window.clearInterval(this.timer)
      // 去掉延迟
      clearTimeout(this.setTimeout)
    },
    methods:{
      back(){
        // 未读完点击返回 标识F，表示未读完
        this.uploadData(false, "F")
        this.$router.push({
          name:"read_speed",
          query:{
            books_selected_class: this.books_selected_class,
            title: this.title,
            thum: this.thum,
            read_single_title: this.read_single_title,
            articleId: this.$route.query.articleId,
            wordCount: this.$route.query.wordCount
          }
        })
      },
      back_(){
        
        // 读完后点击返回 标识T，表示已读完
        this.uploadData(false, "T")
        this.$router.push("books")
      },
      stop_click(){

        if(this.stop_btn_able == true){
          var t = $(".stop").html()
          if(t == "暂停"){
            window.clearInterval(this.timer)
            $(".stop").html("继续")
            $(".cove-stop").css("display", "block")
          }else{
            $(".stop").html("暂停")
            this.start()
            $(".cove-stop").css("display", "none")
          }
        }
      },
      agin_click(){
        this.$router.push({
          name:"read_speed",
          query:{
            books_selected_class: this.books_selected_class,
            title: this.title,
            thum: this.thum,
            read_single_title: this.read_single_title,
            articleId: this.$route.query.articleId,
            wordCount: this.$route.query.wordCount
          }
        })
        // 再读一遍，标识F，表示未读完
        this.uploadData(false, "F")
      },
      delHtmlTag(str){

        var s = str
        s = s.replace(/<p class="ql-align-justify">/g, "　　")
        s = s.replace(/<p class="ql-align-center">/g, "　　")
        s = s.replace(/<p>&nbsp;<\/p>/g, "")
        s = s.replace(/<br>/g, "")
        s = s.replace(/<\/p>/g, "`")
        s = s.replace(/&nbsp;/g, "")
        s = s.replace(/\ +/g,"")
        s = s.replace(/<p>/g, "　　")
        s = s.replace(/,/g, "，")
        s = s.replace(/;/g, "；")
        s = s.replace(/:/g, "：")
        s = s.replace(/"/g, "”")

        var deal_b = s.length
        while(s.indexOf("`") != -1){
          s = this.enter_key(s)
        }
        this.content_sr = s.length / deal_b
        console.log("处理前文字长度：" + deal_b)
        console.log("处理后文字长度：" + s.length)
        console.log("标准比：" + this.content_sr)

        return s
      },
      // 计算回车键（将全角空格填满</p>所在行）
      enter_key(s){
        for(var i = 0; i < s.length; i++){
          if(s.slice(i, i + 1) == "`"){
            var blank = ""
            for(var j = this.word_per_line; j > i % this.word_per_line; j--){
              blank += "　"
              this.blank_qty ++
            }
            if( i % this.word_per_line == 0){
              blank = ""
            }
            s = s.replace(/`/, blank)
            return s
          }
        }
      },
      start(){

        var that = this
        this.timer = window.setInterval(function(){

          that.lookTime ++
          that.accum_time += that.second_to_refresh
          var top_rem = (that.lookTime * 5) / 50
          var top = "calc(" + top_rem + "rem" + " - 100%)"
          console.log("总高度：" + that.content_h + "   " + top + "   " + that.lookTime + "   总用时：" + that.total_time + "   累计用时：" + that.accum_time)

          // 每换新的一页，暂停一秒，预读 (第一页上面已处理过)
          if(that.lookTime <= 1 && that.curr_page > 1){
            window.clearInterval(that.timer)
            setTimeout(function () {
              that.start()
            }, 1000)
          }

          var person = 100 - that.lookTime
          var s_person = "-" + person + "%"
          $(".cover").css('top', s_person)

          // 用来检测最后一页，文字未满一页时，读完就结束
          if(that.accum_time > that.total_time){
            console.log("检测最后一页内容读完，定时器结束")
            window.clearInterval(that.timer)
            $(".cover-").css("display", "block")
            $(".stop").css("display", "none")
            $(".btn").css("display", "none")
          }

          if(that.lookTime >= 100){

            console.log("第：" + that.curr_page + "页完。   总页数：" + that.total_page)
            if(that.total_page > that.curr_page){

              console.log("暂停一秒，下一页")
              that.lookTime = 0
            }else{

              console.log("定时器结束")
              window.clearInterval(that.timer)
            }
            that.curr_page ++
          }
        }, that.second_to_refresh * 1000)
      },
      uploadData(is_first, readFinishFlag){

        var that = this

        // 记录答题信息
        var postData = {
          "studentId": this.$store.state.login_cellphone.id,
          "readDate": this.getNowTime("y-m-d"),
          "articleId": this.$route.query.articleId,
          "readType": "训练",
          "startDate": this.$store.state.record_read.start_time,
          "readFinishFlag": readFinishFlag,
          "trainSpeed": this.word_per_minute
        }
        if(is_first == false){
          postData.endDate = this.getNowTime()
          postData.id = this.$store.state.record_read.id
          postData.trainTime = parseFloat(this.accum_time * 1000 + 3000 + (this.curr_page - 1) * 1000).toFixed(0)
        }
        this.httpRequest_token_no_loading("read/api/v1/appRead/recordReadDetailBook", postData, function(res){
          that.$store.state.record_read.id = res.data
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  .read_content{
    height: 100%;
    overflow: hidden;
    header{
      height: 90/50rem;
      border-bottom: 1/50rem solid  #D9D9D9;
      .btn{
        position: absolute;
        img{
          width: 80/50rem;
        }
      }
      .title{
        width: 100%;
        height: 90/50rem;
        color: white;
        position: absolute;
        z-index: 0; 
        background-color: #1B9BFE;
        line-height: 90/50rem;
        text-align: center;
        span{
          font-size: 25/50rem;
        }
      }
      .stop{
        width: 120/50rem;
        height: 60/50rem;
        line-height: 60/50rem;
        text-align: center;
        background-color: #4da4fa;
        top: 15/50rem;
        bottom: 15/50rem;
        right: 20/50rem;
        position: absolute;
        font-size: 29/50rem;
        color: white;
        border-radius: 5/50rem; 
      }
    }
    .container{
      height: calc(100% - 1.8rem);
      overflow: scroll;
      position: relative;
      .cover{
        width: 100%;
        height: 100%;
        top: -100%;
        background-color: white;
        float: left;
        position: absolute;
        overflow: hidden;
        .cover-bg{
          width: 100%;
          height: 100%;
        }
      }
      .v-for{
        height: 60/50rem;
        text-align: center;
        line-height: 60/50rem;
      }
      .text{
        margin-bottom: 32/50rem;
        margin-right: 32/50rem;
        margin-left: 32/50rem;
        margin-top: 32/50rem;
        color: #333333;
      }
    }
    .cove-stop{
      width: 100%;
      height: 100%;
      background: linear-gradient(0.5turn, #8CD5F5, #EBF9FD);
      z-index: 1;
      display: none;
      margin-top: 90/50rem;
      position: absolute;
      .bg-container{
        width: 360/50rem;
        height: 260/50rem;
        margin: 290/50rem auto 0 auto;
        position: relative;
        img{
          width: 100%;
          position: absolute;
        }
        .cove-t{
          top: 97/50rem;
          left: 160/50rem;
          position: absolute;
          font-size: 37/50rem;
        }
      }
    }
    .cover-{
      width: 100%;
      height: 100%;
      background-color: rgba(235,249,253,1.0);
      position: absolute;
      z-index: 1;
      display: none;
      margin-top: 90/50rem;
      .bg-container{
        width: 580/50rem;
        height: 680/50rem;
        background-color: white;
        margin-top: 146/50rem;
        margin-left: 86/50rem;
        margin-right: 86/50rem;
        border-radius: 16/50rem;
        position: relative;
        text-align: center;
        img{
          width: 100%;
          left: 0;
          position: absolute;
        }
        span{
          position: absolute;
          font-size: 60/50rem;
          color: white;
        }
        .one{
          top: 85/50rem;
          transform: translateX(-50%);
        }
        .two{
          top: 180/50rem;
          transform: translateX(-50%);
        }
        .agin{
          margin-left: 46/50rem;
          margin-right: 46/50rem;
          width: calc(100% - 1.84rem);
          height: 84/50rem;
          background: linear-gradient(0.25turn, #1284fd, #2fabfe);
          margin-top: 420/50rem;
          position: absolute;
          border-radius: 46/50rem;
          color: white;
          font-size: 40/50rem;
          line-height: 84/50rem;
        }
        .back-{
          margin-left: 46/50rem;
          margin-right: 46/50rem;
          width: calc(100% - 1.84rem);
          height: 84/50rem;
          background: linear-gradient(0.25turn, #1284fd, #2fabfe);
          margin-top: 547/50rem;
          position: absolute;
          border-radius: 46/50rem;
          color: white;
          font-size: 40/50rem;
          line-height: 84/50rem;
        }
      }
    }
  }
</style>


