<template>
  <div class="read_qa_result">

    <header_index title='检测成绩' :back_icon='true' @back='back'></header_index>

    <div class="container">

      <img class="container-bg" src="static/img/read/result_bg.png">

      <div class="bg">

        <img class="bg-img" src="static/img/read/bg-qa-result.png">

        <div class="text-container">
          <div class="book-name">
            <img src="static/img/read/i-book-name.png">
            <div class="book-nmae-text-and-word">
              <div class="book-nmae-text">{{ $route.query.title }}</div>
              <div class="book-nmae-word">{{ wordCount }}字</div>
            </div>
          </div>
          <div class="retult-text">
            <div class="left">阅读时间</div><div class="right">{{ dealTime(read_time) }}</div>
            <div class="left">每分钟阅读速度</div><div class="right">{{ parseFloat(wordCount * 60 / read_time).toFixed(0) }}字</div>
            <div class="left">理解力和记忆力</div><div class="right">{{ correct_rate }}%</div>
            <div class="left">阅读效率</div><div class="right">{{ parseFloat((correct_rate / 100) * (wordCount * 60 / read_time)).toFixed(0) }}</div>
          </div>
        </div>

      </div>

    </div>
  </div>
</template>
<script type="text/javascript">
import header_index from '../../components/header.vue'
  export default{
    name:"read_qa_result",
    data(){
      return{
        wordCount: 800,            // 文章字数
        read_time: 0,              // 阅读时间
        accum_time: 0,             // 累计时间
        correct_rate: 80,          // 理解能力
        correct_rate_rank: "",     // 理解能力等级
        read_speed: 200,           // 阅读速度
        article_id: 0              // 文章id
      }
    },
    components:{
      header_index
    },
    created(){

      var that = this

      if(this.$route.query.wordCount){
        this.wordCount = this.$route.query.wordCount
      }
      if(this.$route.query.read_time){
        this.read_time = this.$route.query.read_time
      }
      if(this.$route.query.accum_time){
        this.accum_time = this.$route.query.accum_time
      }
      if(this.$route.query.correct_rate){
        this.correct_rate = this.$route.query.correct_rate
      }
      if(this.$route.query.read_speed){
        this.read_speed = this.$route.query.read_speed
      }
      if(this.$route.query.article_id){
        this.article_id = this.$route.query.article_id
      }

      this.read_speed = parseFloat(this.read_speed).toFixed(0)

      if(this.correct_rate >= 80){
        this.correct_rate_rank = 'a'
      }else if(this.correct_rate >= 70){
        this.correct_rate_rank = 'b'
      }else if(this.correct_rate >= 60){
        this.correct_rate_rank = 'c'
      }else{
        this.correct_rate_rank = 'd'
      }

      // 记录答题信息
      var postData = {
        "studentId": this.$store.state.login_cellphone.id,
        "readDate": this.getNowTime("y-m-d"),
        "articleId": this.article_id,
        "readType": "检测",
        "checkTime": parseFloat(this.accum_time * 1000).toFixed(0),
        "checkSpeed": this.read_speed,
        "checkTopic": this.$route.query.topic_count,
        "checkRightTopic": this.$route.query.correct_count,
        "checkAccuracyRate": this.correct_rate,
        "startDate": this.$store.state.record_read.start_time,
        "endDate": this.getNowTime(),
        "id": this.$store.state.record_read.id,
        "readFinishFlag": "T"
      }
      this.httpRequest_token("read/api/v1/appRead/recordReadDetailBook", postData, function(res){

      })
    },
    methods:{
      back(){

        this.$router.push("books")
      },
      dealTime(s){

        var min = s / 60
        min = Math.floor(min)
        if(min < 10){
          min = "0" + min
        }
        s -= min * 60
        if(s < 10){
          s = "0" + s
        }
        var st = min + ":" + s
        return st
      },
    }
  }
</script>
<style lang="less" scoped>
  .read_qa_result{
    height: 100%;
    overflow: hidden;
    .container{
      height: 100%;
      overflow: hidden;
      .container-bg{
        width: 100%;
        height: 100%;
        position: absolute;
      }
      .bg{
        width: 700/50rem;
        height: 560/50rem;
        margin: 240/50rem auto 0 auto;
        position: relative;
        .bg-img{
          width: 100%;
          position: absolute;
          z-index: 1;
        }
      }
      .text-container{
        width: 100%;
        position: absolute;
        z-index: 2;
        margin-top: 50/50rem;
        .book-name{
          width: 100%;
          height: 120/50rem;
          margin-top: 80/50rem;
          img{
            margin-left: 80/50rem;
            height: 100%;
            float: left;
          }
          .book-nmae-text-and-word{
            width: 340/50rem;
            height: 90/50rem;
            margin: 15/50rem auto 0 auto;
            float: left;
            .book-nmae-text{
              width: 100%;
              height: 80/50rem;
              line-height: 100/50rem;
              text-align: center;
              color: #28B0EE;
              font-weight: bold;
            }
            .book-nmae-word{
              width: 100%;
              height: 10/50rem;
              line-height: 30/50rem;
              text-align: center;
              color: #666666;
              font-size: 27/50rem;
            }
          }
        }
        .retult-text{
          height: 100%;
          &>div{
            height: 100%;
            padding-top: 25/50rem;
            padding-bottom: 20/50rem;
          }
          .left{
            width: 55%;
            float: left;
            margin-left: 90/50rem;
            color: #333333;
            font-size: 26/50rem;
          }
          .right{
            width: calc(45% - 1.8rem);
            float: right;
            font-size: 26/50rem;
          }
        }
      }
    }
  }
</style>


