<template>
  <div class="class">
    
    <header_index title="班级"></header_index>

    <div class="container">
      <div class="create-join-class">
        <div @click='create()'><img src="static/img/class/i-create-class.png"><p>创建班级</p></div>
        <div @click='join()'><img src="static/img/class/i-join-class.png"><p>加入班级</p></div>
      </div>
      <div class="nav"><span>我的班级</span><img src="static/img/read/i-arrow-read.png"></div>
      <div class="my-class">
        <div class="class-info">
          <div class="avatar"><img src="static/img/class/i-adv.png"></div>
          <div class="class-text-info">
            <p>8C</p>
            <div class="class-no-muember">
              <div><p>班级号:21334</p></div>
              <div>成员:10</div>
            </div>
          </div>
        </div>
        <div class="btn">
          <div><img src="static/img/class/i-msg.png"><span>班级通知</span></div>
          <div><img src="static/img/class/i-invite.png"><span>成员邀请</span></div>
        </div>
      </div>
    </div>
    <footer_index/>
  </div>
</template>
<script type="text/javascript">
import header_index from '../../components/header.vue'
import footer_index from '../../components/footer.vue'
  export default{
    name:"class",
    data(){
      return{

      }
    },
    components:{
      header_index,
      footer_index
    },
    created(){

    },
    methods:{
      create(){
        this.$router.push({
          name:"class_create",
        })
      },
      join(){
        this.$router.push({
          name:"class_join",
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  .class{
    overflow: hidden;
    .container{
      overflow: hidden;
      .create-join-class{
        height: 218/50rem;
        &>div{
          height: 226/50rem;
          text-align: center;
          line-height: 57/50rem;
          img{
            width: 100/50rem;
            margin-top: 30/50rem;
          }
          &:nth-child(1){
            float: left;
            width: 50%;
          }
          &:nth-child(2){
            float: right;
            width: 50%;
          }
        }
      }
      .nav{
        margin-top: 60/50rem;
        font-size: 36/50rem;
        margin-left: (32 + 28)/50rem;
        margin-right: 32/50rem;
        img{
          height: 36/50rem;
          vertical-align: middle;
          margin-left: 20/50rem;
        }
      }
      .my-class{
        margin: 44/50rem (32 + 5)/50rem 5/50rem 35/50rem;
        box-shadow: 0 0 3px rgba(153, 153, 153, 0.5);
        .class-info{
          overflow: hidden;
          height: 180/50rem;
          border-bottom: 2/50rem solid  #D9D9D9;
          .avatar{
            padding: 32/50rem;
            float: left;
            img{
              height: 116/50rem;
              vertical-align: middle;
            }
          }
          .class-text-info{
            float: left;
            padding-top: 35/50rem;
            width: calc(100% - 3.6rem);
            .class-no-muember{
              padding-top: 25/50rem;
              &>div{
                &:nth-child(1){
                  float: left;
                }
                &:nth-child(2){
                  float: right;
                  margin-right: 100/50rem;
                }
              }
            }
          }
        }
        .btn{
          width: 100%;
          height: 106/50rem;
          &>div{
            height: 100%; 
            text-align: center;
            line-height: 106/50rem;
            img{
              height: 40/50rem;
              vertical-align: middle;
              margin-right: 15/50rem;
            }
            &:nth-child(1){
              float: left;
              width: 50%;
              position: relative;
              left: 10/50rem;
            }
            &:nth-child(2){
              float: right;
              width: 50%;
              position: relative;
              right: 10/50rem;
            }
          }
        }
      }
    }
  }
</style>


