import Vue from 'vue'
import Router from 'vue-router'

// 登录
import start from '@/pages/login/start'
import login from '@/pages/login/login'
import login_cellphone from '@/pages/login/login_cellphone'
import protocol from '@/pages/login/protocol'

// 首页
import index from '@/pages/index/index'
import recom_books from '@/pages/index/recom_books'
// 消息
import msg from '@/pages/msg/msg'

// 阅读
import read from '@/pages/read/read'
// 课内、课外书籍列表 
import books from '@/pages/read/books'
// 单篇阅读
import read_single from '@/pages/read/read_single'
// 设置阅读速度
import read_speed from '@/pages/read/read_speed'
// 阅读准备_321倒计时
import read_ready from '@/pages/read/read_ready'
// 书籍简介
import book_introdu from '@/pages/read/book_introdu'
// 阅读内容_已设置速度
import read_content from '@/pages/read/read_content'
// 阅读内容_未设置速度
import read_content_qa from '@/pages/read/read_content_qa'
// 阅读测试_答题
import read_qa from '@/pages/read/read_qa'
// 阅读测试_答题结果
import read_qa_result from '@/pages/read/read_qa_result'
// 阅读足迹
import read_his from '@/pages/read/read_his'
// 阅读存折
import read_passbook from '@/pages/read/read_passbook'

// 班级
import classx from '@/pages/class/class'
// 创建班级
import class_create from '@/pages/class/class_create'
// 加入班级
import class_join from '@/pages/class/class_join'

// 我的
import my from '@/pages/my/my'
import personal_info from '@/pages/my/personal_info'
import setting from '@/pages/my/setting'
import about from '@/pages/my/about'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'start',
      component: start
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/login_cellphone',
      name: 'login_cellphone',
      component: login_cellphone
    },
    {
      path: '/protocol',
      name: 'protocol',
      component: protocol
    },
    {
      path: '/index',
      name: 'index',
      component: index
    },
    {
      path: '/recom_books',
      name: 'recom_books',
      component: recom_books
    },
    {
      path: '/msg',
      name: 'msg',
      component: msg
    },
    {
      path: '/read',
      name: 'read',
      component: read
    },
    {
      path: '/books',
      name: 'books',
      component: books
    },
    {
      path: '/read_single',
      name: 'read_single',
      component: read_single
    },
    {
      path: '/read_speed',
      name: 'read_speed',
      component: read_speed
    },
    {
      path: '/read_ready',
      name: 'read_ready',
      component: read_ready
    },
    {
      path: '/book_introdu',
      name: 'book_introdu',
      component: book_introdu
    },
    {
      path: '/read_content',
      name: 'read_content',
      component: read_content
    },
    {
      path: '/read_content_qa',
      name: 'read_content_qa',
      component: read_content_qa
    },
    {
      path: '/read_qa',
      name: 'read_qa',
      component: read_qa
    },
    {
      path: '/read_qa_result',
      name: 'read_qa_result',
      component: read_qa_result
    },
    {
      path: '/read_his',
      name: 'read_his',
      component: read_his
    },
    {
      path: '/read_passbook',
      name: 'read_passbook',
      component: read_passbook
    },
    {
      path: '/class',
      name: 'class',
      component: classx
    },
    {
      path: '/class_create',
      name: 'class_create',
      component: class_create
    },
    {
      path: '/class_join',
      name: 'class_join',
      component: class_join
    },
    {
      path: '/my',
      name: 'my',
      component: my
    },
    {
      path: '/personal_info',
      name: 'personal_info',
      component: personal_info
    },
    {
      path: '/setting',
      name: 'setting',
      component: setting
    },
    {
      path: '/about',
      name: 'about',
      component: about
    }
  ]
})



