<template>
  <div class="personal_info">

    <header_index title='个人资料' :back_icon='true' @back='back'></header_index>

    <div class="container">

      <div class="d-adv">
        <span>头像</span>
        <img class="nav" src="static/img/my/i-push.png">
        <img class="i-adv" src="static/img/my/i-default-adv.png">
      </div>

      <div class="mid">

        <div class="cell">
          <div class="left"></div>
          <div class="mid-">
            <div class="left-t">用户ID</div>
            <div class="right-t">{{ $store.state.login_cellphone.id }}</div>
          </div>
          <div class="right"></div>
        </div>
        
        <div class="cell">
          <div class="left"></div>
          <div class="mid-">
            <div class="left-t">账号</div>
            <div class="right-t">{{ $store.state.login_cellphone.phone }}</div>
          </div>
          <div class="right"></div>
        </div>

        <div class="cell">
          <div class="left"></div>
          <div class="mid-">
            <div class="left-t">昵称</div>
            <img class="nav" src="static/img/my/i-push.png">
            <div class="right-t mar-right">{{ nick_name }}</div>
          </div>
          <div class="right"></div>
        </div>

        <div class="cell" @click="modi_y_click('name')">
          <div class="left"></div>
          <div class="mid-">
            <div class="left-t">姓名</div>
            <img class="nav" src="static/img/my/i-push.png">
            <div class="right-t mar-right">{{ na_me }}</div>
          </div>
          <div class="right"></div>
        </div>

        <div class="cell" @click="modi_y_click('school')">
          <div class="left"></div>
          <div class="mid-">
            <div class="left-t">学校</div>
            <img class="nav" src="static/img/my/i-push.png">
            <div class="right-t mar-right">{{ schoo_l }}</div>
          </div>
          <div class="right"></div>
        </div>

        <div class="cell" @click="modi_y_click('grade')">
          <div class="left"></div>
          <div class="mid-">  
            <div class="left-t">年级</div>
            <img class="nav" src="static/img/my/i-push.png">
            <div class="right-t mar-right">{{ grad_e }}</div>
          </div>
          <div class="right"></div>
        </div>

      </div>

      <div class="mid bottom">

        <div class="cell">
          <div class="left"></div>
          <div class="mid-">
            <div class="left-t">会员类型</div>
            <div class="right-t">VIP</div>
          </div>
          <div class="right"></div>
        </div>

        <div class="cell">
          <div class="left"></div>
          <div class="mid-">
            <div class="left-t">VIP到期日</div>
            <div class="right-t">2020-03-01</div>
          </div>
          <div class="right"></div>
        </div>

        <div class="cell">
          <div class="left"></div>
          <div class="mid-">
            <div class="left-t">微信号</div>
            <img class="nav" src="static/img/my/i-push.png">
            <div class="right-t mar-right">未设置</div>
          </div>
          <div class="right"></div>
        </div>

      </div>
		
    </div>
    <footer_index_no_menu/>
  </div>
</template>
<script type="text/javascript">
import header_index from '../../components/header.vue'
import footer_index_no_menu from '../../components/footer_no_menu.vue'
  export default{
    name:"personal_info",
    data(){
      return{

        nick_name: "请填写您的昵称",
        na_me: "请填写您的姓名",
        schoo_l: "请填写所在学校",
        grad_e: "请填写所在年级",
      }
    },
    components:{
      header_index, 
      footer_index_no_menu
    },
    created(){

      var that = this

      this.request_info()


    },
    methods:{

      back(){

        this.$router.push("my")
      },
      modi_y_click(key){
        this.$prompt('请输入姓名', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(({ value }) => {

          this.modify(key, value)
          this.$message.success("修改成功")
        }).catch(() => { })
      },
      request_info(){

        var that = this

        var postData = {
          "id": this.$store.state.login_cellphone.id
        }
        this.httpRequest_token_no_loading("user/api/v1/app/queryStudentById", postData, function(res){

          that.$store.state.login_cellphone = res.data
          if(res.data.name.length > 0){

            that.na_me = res.data.name
          }
          if(res.data.school.length > 0){
            
            that.schoo_l = res.data.school
          }
          if(res.data.grade.length > 0){
            
            that.grad_e = res.data.grade
          }
        })
      },
      modify(key, value){

        var that = this

        var postData = {
          [key]: value,
          "id": this.$store.state.login_cellphone.id,
        }
        this.httpRequest_token("user/api/v1/app/updateStudentByJSON", postData, function(res){

          that.request_info()
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  .personal_info{
    height: 100%;
    overflow: hidden;
    .container{
      height: 100%;
      overflow: hidden;
      background: linear-gradient(0.5turn, #8CD5F5, #EBF9FD);
      .d-adv{
        width: 100%;
        height: 160/50rem;
        background: rgba(255, 255, 255, 0.85);
        line-height: 160/50rem;
        color: #666666;
        font-size: 29/50rem;
        padding-left: 30/50rem;
        .nav{
          height: 100%;
          float: right;
          margin-right: 60/50rem;
        }
        .i-adv{
          height: 100/50rem;
          float: right;
          margin-top: 30/50rem;
          margin-right: 20/50rem;
        }
      }
      .mid{
        margin-top: 20/50rem;
        .cell{
          width: 100%;
          height: 90/50rem;
          background: rgba(255, 255, 255, 0.85);
          .left{
            width: 30/50rem;
            height: 100%;
            float: left;
          }
          .mid-{
            width: 690/50rem;
            height: 89/50rem;
            color: #666666;
            line-height: 90/50rem;
            border-bottom: 1/50rem solid #DDDDDD;
            float: left;
            .left-t{
              float: left;
              font-size: 29/50rem;
            }
            .right-t{
              float: right;
              color: #BBBBBB;
              font-size: 29/50rem;
            }
            .mar-right{
              margin-right: 20/50rem;
            }
            .nav{
              height: 100%;
              float: right;
            }
          }
          .right{
            width: 30/50rem;
            float: right;
          }
        }
      }
      .bottom{
        margin-top: 28/50rem;
      }
    }
  }
</style>


