<template>
  <div class="read_content_qa">

    <header>
      <div class="title">
        {{ title }}<span>（{{ $route.query.wordCount }}字）</span>
      </div>
      <div class="btn">
        <img @click='back()' src="static/img/header/i-back.png">
      </div>
      <div class="timer">
        <span>{{ dealTime(accum_time) }}</span>
      </div>
    </header>

    <div class="container">

      <div class="text" v-html="data.content"></div>

      <div class="cover"><img @click="back" src="static/img/read/bg-stopped.png"></div>

      <div class="buttom"><img class="bg" src="static/img/footer/bg-tarbar.png">
        <div class="again" @click='again()'>
          <img src="static/img/read/i-btn-whole-read-.png">
          <span>再读一遍</span>
        </div>
        <div class="exam" @click='exam()'>
          <img src="static/img/read/i-btn-single-read-.png">
          <span>立即检测</span>
        </div>
      </div>

    </div>
  </div>
</template>
<script type="text/javascript">
import $ from "jquery"
  export default{
    name:"read_content_qa",
    data(){
      return{
        title: "",
        books_selected_class: "",
        thum: "",                   //略缩图
        read_single_title:"",       //标题
        data: {"content":""},       //数据
        timer: "",                  //定时器
        wordCount: 0,               //文章字数
        accum_time: 0,              //累计用时(秒)
        stop_png_name: "i-stop.png" //暂停图片名
      }
    },
    created(){

      var that = this

      this.$store.state.record_read.start_time = this.getNowTime()

      if(this.$route.query.title){
        this.title = this.$route.query.title
      }
      if(this.$route.query.books_selected_class){
        this.books_selected_class = this.$route.query.books_selected_class
      }
      if(this.$route.query.thum){
        this.thum = this.$route.query.thum
      }
      if(this.$route.query.read_single_title){
        this.read_single_title = this.$route.query.read_single_title
      }
      if(this.$route.query.wordCount){
        this.wordCount = this.$route.query.wordCount
      }

      console.log(that.data)

      if(that.data.content.length == 0){

        var postData = {
          "articleId": this.$route.query.articleId
        }
        this.httpRequest_token("read/api/v1/appRead/getArticleById", postData, function(res){

          that.data = res.data
          that.data.content = that.delHtmlTag(that.data.content)

          // 解决最后一行文字被遮挡问题
          that.data.content += "<div style='width:120px; height:60px;'></div>"

          that.start()
          that.uploadData(true, "F")
        })
      }
    },
    beforeDestroy(){

      console.log("对象准备死掉")
      // 去除定时器
      window.clearInterval(this.timer)  
    },
    methods:{
      back(){
        this.uploadData(false, "F")
        this.$router.push({
          name:"books",
          query:{
            books_selected_class: this.books_selected_class,
            title: this.title,
            thum: this.thum,
            read_single_title: this.read_single_title,
            wordCount: this.wordCount,
            articleId: this.$route.query.articleId
          }
        })
      },
      stop_click(){

        var t = $(".stop").html()

        if(t.indexOf("stop") !== -1){
          window.clearInterval(this.timer)
          this.stop_png_name = "i-start.png"
          $(".cover").css("display", "block")
        }else{
          this.start()
          this.stop_png_name = "i-stop.png"
          $(".cover").css("display", "none")
        }
      },
      delHtmlTag(str){

        var s = str
        // s = str.replace(/<p>&nbsp;<\/p>/g, "")
        return s
      },
      start(){

        var that = this
        this.timer = window.setInterval(function(){

          that.accum_time ++
        }, 1000)
      },
      dealTime(s){

        var min = s / 60
        min = Math.floor(min)
        if(min < 10){
          min = "0" + min
        }
        s -= min * 60
        if(s < 10){
          s = "0" + s
        }
        var st = min + ":" + s
        return st
      },
      again(){

      },
      exam(){

        this.uploadData(false, "F")

        if(this.data.stock.topicList.length == 0){

          this.$toast({message:"未找到题目...", type:'', position:'bottom', duration:'2000'})
          return;
        }
        
        this.$router.push({
          name:"read_qa",
          query:{
            topicList: this.data.stock.topicList,
            index: 1,
            wordCount: this.wordCount,
            accum_time: this.accum_time,
            article_id: this.data.id,
            title: this.title
          }
        })
      },
      uploadData(is_first, readFinishFlag){

        var that = this

        // 记录答题信息
        var postData = {
          "studentId": this.$store.state.login_cellphone.id,
          "readDate": this.getNowTime("y-m-d"),
          "articleId": this.$route.query.articleId,
          "readType": "检测",
          "startDate": this.$store.state.record_read.start_time,
          "readFinishFlag": readFinishFlag
        }
        if(is_first == false){
          postData.endDate = this.getNowTime()
          postData.id = this.$store.state.record_read.id
          postData.checkTime = parseFloat(this.accum_time * 1000).toFixed(0)
        }
        this.httpRequest_token_no_loading("read/api/v1/appRead/recordReadDetailBook", postData, function(res){
          that.$store.state.record_read.id = res.data
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  .read_content_qa{
    height: 100%;
    overflow: hidden;
    header{
      height: 90/50rem;
      border-bottom: 1/50rem solid  #D9D9D9;
      .btn{
        position: absolute;
        img{
          width: 80/50rem;
        }
      }
      .title{
        width: 100%;
        height: 90/50rem;
        color: white;
        position: absolute;
        z-index: 0; 
        background-color: #1B9BFE;
        line-height: 90/50rem;
        text-align: center;
        span{
          font-size: 25/50rem;
        }
      }
      .timer{
        width: 90/50rem;
        height: 80/50rem;
        top: 5/50rem;
        bottom: 5/50rem;
        right: 10/50rem;
        position: absolute;
        span{
          line-height: 80/50rem;
          font-size: 27/50rem;
          color: white;
        }
      }
    }
    .container{
      overflow: hidden;
      height: calc(100% - 1.8rem);
      position: relative;
      background-color: #EBF9FD;
      .text{
        height: calc(100% - 1.3rem);
        overflow: scroll;
        padding-bottom: 32/50rem;
        padding-right: 32/50rem;
        padding-left: 32/50rem;
        padding-top: 32/50rem;
        color: #00557B;
        position: absolute;
        line-height: 90/50rem;
        font-size: 20/50rem;
        .test-btn{
          width: 120/50rem;
          height: 80/50rem;
          margin-top: 100/50rem;
          background-color: red;

        }
        p{
          text-indent: 2rem;
        }
      }
      .buttom{
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 140/50rem;
        .bg{
          width: 100%;
          height: 100%;
        }
        span{
          line-height: 85/50rem;
          display: block;
          text-align: center;
          color: white;
          position: relative;
        }
        .again{
          width: 260/50rem;
          height: 97/50rem;
          position: absolute;
          bottom: 10/50rem;
          left: 75/50rem;
          img{
            width: 100%;
            height: 100%;
            position: absolute;
          }
        }
        .exam{
          width: 260/50rem;
          height: 97/50rem;
          position: absolute;
          bottom: 10/50rem;
          right: 75/50rem;
          img{
            width: 100%;
            height: 100%;
            position: absolute;
          }
        }
      }
      .cover{
        width: 100%;
        height: 100%;
        background-color: white;
        display: none;
        position: relative;
        img{
          width: 100%;
        }
      }
    }
  }
</style>


