<template>
  <div class="read_his">

    <header_index title='阅读足迹' :back_icon='true' @back='back'></header_index>

    <div class="container">

      <div class="v-for" v-for='(item, index) in datas' :id="index" :key='index'>
        <div class="month">
          <div class="month-"><div class="month-text">{{ item.month }}</div></div>

          <div v-for='(obj, idx) in item.detail' :id="idx" :key='idx'>
            <div class="days">
              <div class="day-left-line">
                <div v-if="idx != 0" class="line-top"></div>
                <div v-if="idx == 0" class="line-top-none"></div>
                <div class="line-circle"></div>
                <div class="line-bottom"></div>
              </div>
              <div class="day-content">
                <div class="one">{{ obj.readDate }}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{ obj.readType }}</div>
                <div class="two">{{ obj.title }}</div>
                <div class="three">{{ obj.startDate }}-{{ obj.endDate }}<span>{{ obj.trainAndCheckTimeStr }}</span></div>
                <div class="separator-bottom"></div>
              </div>
            </div>
          </div>

        </div>
      </div>
		
    </div>
  </div>
</template>
<script type="text/javascript">
import header_index from '../../components/header.vue'
  export default{
    name:"read_his",
    data(){
      return{
        // datas:[{"month":"2020-02","content":[{"title":"白鹭","read_date":"02-13","type":"单篇阅读","start_time":"12:59:59","end_time":"13:05:59","total_time":"5分10秒"},{"title":"将相和","read_date":"02-13","type":"单篇检测","start_time":"12:59:59","end_time":"13:05:59","total_time":"5分10秒"},   {"title":"白鹭","read_date":"02-13","type":"单篇阅读","start_time":"12:59:59","end_time":"13:05:59","total_time":"5分10秒"},{"title":"将相和","read_date":"02-13","type":"单篇检测","start_time":"12:59:59","end_time":"13:05:59","total_time":"5分10秒"}]},   {"month":"2020-01","content":[{"title":"白鹭","read_date":"02-13","type":"单篇阅读","start_time":"12:59:59","end_time":"13:05:59","total_time":"5分10秒"},{"title":"将相和","read_date":"02-13","type":"单篇检测","start_time":"12:59:59","end_time":"13:05:59","total_time":"5分10秒"}]}]
        datas: []
      }
    },
    components:{
      header_index,
    },
    created(){

      var that = this
      var postData = {
        "studentId": this.$store.state.login_cellphone.id,
        "month": this.getNowTime("y-m")
      }
      this.httpRequest_token("read/api/v1/appRead/getReadDetailBookListGroupMonth", postData, function(res){

        that.datas = res.data
      })

    },
    methods:{
      back(){

        this.$router.push("index")
      }
    }
  }
</script>
<style lang="less" scoped>
  .read_his{
    height: 100%;
    overflow: hidden;
    .container{
      height: calc(100% - 1.8rem);
      overflow: scroll;
      .v-for{
        .month{
          width: 100%;
          margin-top: 2/50rem;
          .month-{
            width: 100%;
            height: 90/50rem;
            background-color: #EBF9FD;
            overflow: hidden;
            .month-text{
              width: 170/50rem;
              height: 45/50rem;
              background-color: #F2AA3F;
              margin-top: 22.5/50rem;
              border-radius: 0 45/50rem 45/50rem 0;
              color: white;
              font-size: 27/50rem;
              line-height: 45/50rem;
              text-align: center;
            }
          }
          .days{
            width: 100%;
            height: 200/50rem;
            .day-left-line{
              width: 104/50rem;
              height: 100%;
              overflow: hidden;
              float: left;
              .line-top{
                width: 2/50rem;
                height: 38/50rem;
                background-color:#1B9BFE;
                margin-left: 38/50rem;
              }
              .line-top-none{
                width: 2/50rem;
                height: 38/50rem;
                margin-left: 38/50rem;
              }
              .line-circle{
                width: 16/50rem;
                height: 16/50rem;
                background-color: #1B9BFE;
                border-radius: 16/50rem;
                margin-left: 31/50rem;
              }
              .line-bottom{
                width: 2/50rem;
                height: 147/50rem;
                background-color: #1B9BFE;
                margin-left: 38/50rem;
              }
            }
            .day-content{
              width: (750 - 104)/50rem;
              height: 100%;
              position: relative;
              float: right;
              .one{
                width: 100%;
                height: 66/50rem;
                color: #818080;
                font-size: 26/50rem;
                line-height: 85/50rem;
              }
              .two{
                width: 100%;
                height: 32.5%;
                font-size: 31/50rem;
                line-height: 66/50rem;
                font-size: 30 /50rem;
              }
              .three{
                width: 100%;
                height: 32.5%;
                line-height: 50/50rem;
                font-size: 26/50rem;
                color: #818080;
                span{
                  float: right;
                  margin-right: 28/50rem;
                }
              }
              .separator-bottom{
                width: 100%;
                height: 2/50rem;
                position: absolute;
                bottom: 0;
                background-image: linear-gradient(to right, #ccc 0%, #ccc 50%, transparent 50%);
                background-size: 20/50rem 4/50rem;
                background-repeat: repeat-x;
              }
            }
          }
        }
      }
    }
  }
</style>


