<template>
  <div class="index">
    <header>
      <div class="msg"><img src="static/img/index/i-msg.png"></div>
      <div class="search">
        <div class="search-">
          <div v-if="search_key == ''" class="propmt">   
            <img src="static/img/index/i-search.png">
            <span>搜索</span>
          </div>
          <input class="input" v-model="search_key">
        </div>
      </div>
      <div class="history"><img src="static/img/index/i-history.png" @click='toHistory()'></div>
    </header>
    <div class="container">

      <div class="scroll">

        <div class="banner">
          <el-carousel trigger="click" height="150px">
            <el-carousel-item>
              <img style="width: 100%" src="static/img/index/banner-1.png">
            </el-carousel-item>
            <el-carousel-item>
              <img style="width: 100%" src="static/img/index/banner-2.png">
            </el-carousel-item>
          </el-carousel>
        </div>

        <div class="single-whole-read">
          <div class="single" @click='single()'><img src="static/img/index/i-single.png"></div>
          <div class="whole" @click='whole()'><img src="static/img/index/i-whole.png"></div>
        </div>

        <div class="temp">
          <div class="recom-img"><img src="static/img/index/temp.png"></div>
          <div class="recom-click" @click='recom_click()'></div>
        </div>

        <div class="kong"></div>

      </div>

    </div>
    <footer_index/>
  </div>
</template>
<script type="text/javascript">
import footer_index from '../../components/footer.vue'
  export default{
    name:"index",
    data(){
      return{
        search_key: ""
      }
    },
    components:{
      footer_index
    },
    created(){

    },
    methods:{
      single(){

        this.$router.push("read_single")
      },
      whole(){

        this.$toast({message:"整本阅读", type:'', position:'bottom', duration:'1500'})
      },
      recom_click(){

        this.$router.push("recom_books")
      },
      toHistory(){

        this.$router.push("read_his")
      }
    }
  }
</script>
<style lang="less" scoped>
  .index{
    width: 100%;
    height: 100%;
    overflow: hidden;
    header{
      height: 90/50rem;
      border-bottom: 1/50rem solid #D9D9D9;
      background-color: #1B9BFE;
      &>div{
        height: 100%;
        float: left;
        text-align: center;
        line-height: 122.5/50rem;
        img{
          width: 50%;
        }
      }
      .msg{
        width: 100/50rem;
      }
      .search{
        width: 550/50rem;
        .search-{
          width: 100%;
          height: 60/50rem;
          margin: 0 auto;
          margin-top: 15/50rem;
          border-radius: 8/50rem;
          text-align: left;
          line-height: 60/50rem;
          color: #B3B3B3;
          background-color: white;
          .propmt{
            position: absolute;
            font-size: 27/50rem;
            img{
              width: 25/50rem;
              height: 25/50rem;
              margin-left: 16/50rem;
              vertical-align: middle;
              margin-top: -5/50rem;
            }
          }
          .input{
            width: 100%;
            height: 100%;
            padding-left: 16/50rem;
          }
        }
        input::-ms-input-placeholder {
          font-size: 32/50rem;
          color: #999999;
        }
        input{
          outline: none;
          background: transparent;
          border: none;
          height: 100%;
          width: 100%;
        }
        input::-webkit-input-placeholder {
          font-size: 32/50rem;
          color: #999999;
        }
        input::-moz-input-placeholder {
          font-size: 32/50rem;
          color: #999999;
        }
      }
      .history{
        width: 100/50rem;
      }
    }
    .container{
      width: 100%;
      height: 100%;
      background: linear-gradient(0.5turn, #8CD5F5, #EBF9FD);
      .scroll{
        height: 100%;
        overflow: scroll;
        .banner{
          width: 100%;
          height: 280/50rem;
          overflow: hidden;
        }
        .single-whole-read{
          height: 173/50rem;
          margin-top: 17/50rem;
          margin-left: 15/50rem;
          margin-right: 15/50rem;
          &>div{
            width: 356/50rem;
            height: 100%;
          }
          .single{
            float: left;
            img{
              width: 100%;
            }
          }
          .whole{
            float: right;
            img{
              width: 100%;
            }
          }
        }
        .temp{
          height: 700/50rem;
          margin-top: 18/50rem;
          margin-left: 15/50rem;
          margin-right: 15/50rem;
          .recom-img{
            float: left;
            img{
              width: 100%;
            }
          }
          .recom-click{
            right: 10/50rem;
            position: absolute;
            width: 140/50rem;
            height: 100/50rem;
          }
        }
        .kong{
          height: 240/50rem;
        }
      }
    }
  }
</style>


